import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    status: "idle",
    error: "",
    inputs: {
        oldPassword: "",
        newPassword: "",
        confirmNewPassword: "",
    },
}

const slice = createSlice({
    initialState,
    name: "changePassword",
    reducers: {
        loadChangePassword: (state, action) => {
            state.inputs.oldPassword = action.payload.oldPassword
            state.inputs.newPassword = action.payload.newPassword
            state.inputs.confirmNewPassword = action.payload.confirmNewPassword
            state.status = "LOADING"
        },
        loadChangePasswordSuccess: (state) => {
            state.status = "SUCCESS"
            state.inputs.oldPassword = ""
            state.inputs.newPassword = ""
            state.inputs.confirmNewPassword = ""
        },
        loadChangePasswordError: (state) => {
            state.status = "ERROR"
            state.inputs.oldPassword = ""
            state.inputs.newPassword = ""
            state.inputs.confirmNewPassword = ""
        },
    },
})


const { reducer, actions } = slice
export const { loadChangePassword, loadChangePasswordSuccess, loadChangePasswordError } = actions
export default reducer