export const getStatus = (state) => {
    return state.articleEditingPlatform.articleEditor.status
}

export const getIsAuthor = (state) => {
    return state.articleEditingPlatform.articleEditor.isAuthor
}

export const getCurrEditingArticleId = (state) => {
    return state.articleEditingPlatform.articleEditor.currArticleId
}

export const getCurrServerArticle = (state) => {
    return state.articleEditingPlatform.articleEditor.serverArticle;
}


export const getCurrEditingArticle = (state) => {
    return state.articleEditingPlatform.articleEditor.currArticle;
}

export const getCurrEditingArticleTitle = (state) => {
    return state.articleEditingPlatform.articleEditor.currArticle.title;
}

export const getCurrEditingArticleDate = (state) => {
    return state.articleEditingPlatform.articleEditor.currArticle.date;
}

export const getCurrEditingArticleTemplate = (state) => {
    return state.articleEditingPlatform.articleEditor.currArticle.author;
}

export const getCurrEditingArticleContent = (state) => {
    return state.articleEditingPlatform.articleEditor.currArticle.content;
}

export const getCurrEditingArticleCategory = (state) => {
    return state.articleEditingPlatform.articleEditor.currArticle.author;
}

export const getCurrEditingArticleWordCount = (state) => {
    return state.articleEditingPlatform.articleEditor.currArticle.author;
}

export const isServerArticleError = (state) => {
    return state.articleEditingPlatform.articleEditor.loadingStatus === "ERROR"
}

export const isUpdateEditingArticleSuccess = (state) => {
    return state.articleEditingPlatform.articleEditor.updatingStatus === "SUCCESS"
}

export const isUpdateEditingArticleError = (state) => {
    return state.articleEditingPlatform.articleEditor.updatingStatus === "ERROR"
}

export const isDeleteEditingArticleSuccess = (state) => {
    return state.articleEditingPlatform.articleEditor.deletingStatus === "SUCCESS"
}

export const isDeleteEditingArticleError = (state) => {
    return state.articleEditingPlatform.articleEditor.deletingStatus === "ERROR"
}

export const isDeleteEditingArticleIdle = (state) => {
    return state.articleEditingPlatform.articleEditor.deletingStatus === "idle"
}