import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    status: "idle",
    error: "",
}

const slice = createSlice({
    initialState,
    name: "createNewArticle",
    reducers: {
        loadCreateNewArticle: (state) => {
            state.status = "LOADING"
        },
        loadSuccess: (state) => {
            state.status = "SUCCESS"
        },
        loadError: (state) => {
            state.status = "ERROR"
        },
        loadIdle: (state) => {
            state.status = "idle"
        }
    },
})


const { reducer, actions } = slice
export const { loadCreateNewArticle, loadSuccess, loadError, loadIdle } = actions
export default reducer