import React from 'react'
import "./index.scss";
import ReactModal from 'react-modal';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faClose } from '@fortawesome/free-solid-svg-icons';

export default function Modal(props) {
    const {height, width, show, handleShow, children} = props
    // console.log(((window.innerHeight - height) / 2) / window.innerHeight * 100)

    return (
        <ReactModal isOpen={show} ariaHideApp={false}
            style={{
                overlay : {
                    backgroundColor: 'hsla(0,0%,40%,0.3)'
                },
                content : {
                    position: 'absolute',
                    top: `${((window.innerHeight - height) / 2) / window.innerHeight * 100}%`, // Modal is in center of screen // 10%
                    left: '50%',
                    transform: 'translate(-50%, 0)',
                    width: `${width}px`,
                    height: `${height}px`,
                    backgroundColor: '#ffffff',
                    display: 'flex',
                    flexDirection: "column",
                },
            }}
        >
            <div className='modal-background'>
                <div className='modal-close'>
                    <FontAwesomeIcon icon={faClose} onClick={() => handleShow()}/>
                </div>
                {children}
            </div>
        </ReactModal>
    )
}
