import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../services/api";
import { loadSuccess, loadError } from ".";
import { getUserId } from "../../login/user/selector";
import { getCurrentGeneralArticlesPage, getFilters, getGeneralArticlesPerPage } from "./selector";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* generalArticlesSaga() {
    // console.log("poo")
    try {
        const userId = yield select(getUserId)
        const filters = yield select(getFilters)
        const generalArticlesPerPage = yield select(getGeneralArticlesPerPage)
        const currentGeneralArticlesPage = yield select(getCurrentGeneralArticlesPage)
        const generalArticles = yield call([api, api.getGeneralArticlesData], userId, filters, generalArticlesPerPage, currentGeneralArticlesPage) 
        yield put(loadSuccess(generalArticles))
    } catch (e) {
        yield put(loadError(getResponseError(e)))
        console.log("Error from User Office Saga: ", e)
    }

}