import React, { useEffect, useState } from 'react'
import './index.scss'
import Button from '../../components/Button'
import HeaderFooter from '../../layout/headerfooter'
import { connect } from 'react-redux'
import { getPendingArticlesPerPage, getPendingArticles, getCurrentPendingArticlesPage, getTotalPendingArticles } from '../../../store/reducer/userOffice/pendingArticles/selector'
import { loadPendingArticles, changeCurrentPendingArticlePage, changePendingArticlesPerPage } from '../../../store/reducer/userOffice/pendingArticles/index'
import { loadGeneralArticles, changeCurrentGeneralArticlePage, changeGeneralArticlesPerPage, changeGeneralArticlesFilters } from '../../../store/reducer/userOffice/generalArticles/index'
import PendingArticle from './PendingArticle'
import Pagination from '../../components/Pagination'
import { getCurrentGeneralArticlesPage, getGeneralArticles, getTotalGeneralArticles, getGeneralArticlesPerPage, getFilters } from '../../../store/reducer/userOffice/generalArticles/selector'
import GeneralArticle from './GeneralArticle'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import "react-datepicker/dist/react-datepicker.css";
import GeneralArticlesModal from '../../modals/GeneralArticlesModal'
import { isUserWriter } from '../../../store/reducer/login/user/selector'
import { loadCreateNewArticle, loadIdle } from '../../../store/reducer/userOffice/createNewArticle'
import { isCreateNewArticleError, isCreateNewArticleSuccess } from '../../../store/reducer/userOffice/createNewArticle/selector'
import { toast } from 'react-toastify'

const matchStateToProps = (state) => ({
    isUserWriter: isUserWriter(state),
    isCreateNewArticleSuccess: isCreateNewArticleSuccess(state),
    isCreateNewArticleError: isCreateNewArticleError(state),

    pendingArticles: getPendingArticles(state),
    currentPendingArticlesPage: getCurrentPendingArticlesPage(state),
    pendingArticlesPerPage: getPendingArticlesPerPage(state),
    totalPendingArticles: getTotalPendingArticles(state),

    currentFilters: getFilters(state),
    generalArticles: getGeneralArticles(state),
    currentGeneralArticlesPage: getCurrentGeneralArticlesPage(state),
    generalArticlesPerPage: getGeneralArticlesPerPage(state),
    totalGeneralArticles: getTotalGeneralArticles(state),
})

const matchDispatchToProps = {
    loadCreateNewArticle,
    loadIdle,

    loadPendingArticles,
    changePendingArticlesPerPage, changeCurrentPendingArticlePage,

    loadGeneralArticles,
    changeCurrentGeneralArticlePage, changeGeneralArticlesPerPage, changeGeneralArticlesFilters
}

function UserOffice(props) {
    const {
        loadCreateNewArticle,
        loadIdle,
        isCreateNewArticleSuccess,
        isCreateNewArticleError,

        isUserWriter,
        loadPendingArticles, pendingArticles,
        currentPendingArticlesPage, changeCurrentPendingArticlePage, changePendingArticlesPerPage, totalPendingArticles, pendingArticlesPerPage,

        loadGeneralArticles, generalArticles,
        currentGeneralArticlesPage, changeCurrentGeneralArticlePage, changeGeneralArticlesPerPage, totalGeneralArticles, generalArticlesPerPage,

        currentFilters,
        changeGeneralArticlesFilters
    } = props

    const [showGeneralArticlesModal, setShowGeneralArticlesModal] = useState(false);

    const [articleOwnershipFilters, setArticleOwnershipFilters] = useState(["All Articles", "My Articles"]);
    const [articleStatusFilters, setArticleStatusFilters] = useState(["editing", "pending", "rejected", "published"]);
    const [articleCategoryFilters, setArticleCategoryFilters] = useState(["news", "opinion", "sports", "entertainment", "clubs", "advertisement"]);
    const [datePickerStartDate, setDatePickerStartDate] = useState("");
    const [datePickerEndDate, setDatePickerEndDate] = useState("");

    const handleCreateNewArticle = () => {
        loadCreateNewArticle()
    }

    const handleChangeGeneralArticlesFilters = () => {
        changeGeneralArticlesFilters({
            ownership: articleOwnershipFilters,
            status: articleStatusFilters,
            category: articleCategoryFilters,
            date: {
                start: datePickerStartDate === null ? "" : datePickerStartDate.toString(),
                end: datePickerEndDate === null ? "" : datePickerEndDate.toString(),
            }
        })
        changeCurrentGeneralArticlePage(1)
    }

    const handleDiscardFilterChanges = () => {
        setArticleOwnershipFilters(currentFilters.ownership)
        setArticleStatusFilters(currentFilters.status)
        setArticleCategoryFilters(currentFilters.category)
        setDatePickerStartDate(currentFilters.date.start)
        setDatePickerEndDate(currentFilters.date.end)
    }

    useEffect(() => {
        loadPendingArticles()
    }, [loadPendingArticles, currentPendingArticlesPage, pendingArticlesPerPage])

    useEffect(() => {
        loadGeneralArticles()
    }, [loadGeneralArticles, currentGeneralArticlesPage, generalArticlesPerPage, showGeneralArticlesModal])

    useEffect(() => {
        if (isCreateNewArticleSuccess) {
            toast.success("Created a New Article!")
            loadIdle()
        }
    }, [isCreateNewArticleSuccess, loadIdle])

    useEffect(() => {
        if (isCreateNewArticleError) {
            toast.error("Failed to Create New Article! ")
            loadIdle()
        }
    }, [isCreateNewArticleError, loadIdle])

    console.log(generalArticles[0])

    return (
        <HeaderFooter title="left">
            <div className='user-office'>
                <div className='create-new-article'>
                    <Button size="long" onClick={handleCreateNewArticle}>
                        Create New Article
                    </Button>
                </div>

                {
                    !isUserWriter && 
                    <div className='pending-articles-to-publish-box'>
                        <div className="pending-articles-to-publish-label">
                            Pending Articles to <strong>Publish</strong>
                        </div>
                        <div className='pending-articles-to-publish-scrollbar'>
                            <div className='pending-articles-to-publish'>
                                {pendingArticles.map((article) => (
                                    <div key={article.id} className='pending-article-to-publish'>
                                        <PendingArticle
                                            id={article.id}
                                            img={article.img}
                                            title={article.title}
                                            authorId={article.authorId}
                                            authorName={article.authorName}
                                            date={article.date}
                                            category={article.category}
                                            wordcount={article.wordcount}
                                        />
                                    </div>

                                ))}
                            </div>
                        </div>
                        <Pagination
                            values={[5, 10]}
                            currentPage={currentPendingArticlesPage}
                            changeCurrentPage={changeCurrentPendingArticlePage}
                            changeItemsPerPage={changePendingArticlesPerPage}
                            itemsPerPage={pendingArticlesPerPage}
                            totalNumItems={totalPendingArticles}
                        />
                    </div>
                }

                <div className='general-articles-box'>
                    <div className="general-articles-label">
                        <div>
                            {!isUserWriter ? "General Articles" : "Your Articles"}
                        </div>
                        <div>
                            <div className="general-articles-filters">
                                <FontAwesomeIcon icon={faBars} onClick={() => { setShowGeneralArticlesModal(true) }} />
                            </div>

                        </div>

                    </div>
                    <div className='general-articles-scrollbar'>
                        <table className='general-articles'>
                            <tbody className='general-articles-heading'>
                                <tr>
                                    <th className='title'>Title</th>
                                    <th>Date Accessed</th>
                                    <th>Author</th>
                                    <th>Category</th>
                                    <th>Words</th>
                                    <th>Status</th>
                                </tr>
                            </tbody>
                            <tbody>
                                {generalArticles.length > 0 ? generalArticles.map((article) => (
                                    <GeneralArticle // component is a table row
                                        key={article.id}
                                        id={article.id}
                                        img={article.img}
                                        title={article.title}
                                        authorName={article.authorName}
                                        date={article.date}
                                        category={article.category}
                                        wordcount={article.wordcount}
                                        status={article.status}
                                    />
                                )) : <tr className='general-articles-no-articles'>
                                    <th className='img-title'>No Articles</th>
                                    <th className='date'></th>
                                    <th className='author'></th>
                                    <th className='category'></th>
                                    <th className='wordcount'></th>
                                    <th className='status'></th>
                                </tr>}
                            </tbody>
                        </table>
                    </div>

                    <div className="general-articles-column-pagination">
                        <Pagination
                            values={[5, 10, 15, 20]}
                            currentPage={currentGeneralArticlesPage}
                            changeCurrentPage={changeCurrentGeneralArticlePage}
                            changeItemsPerPage={changeGeneralArticlesPerPage}
                            itemsPerPage={generalArticlesPerPage}
                            totalNumItems={totalGeneralArticles}
                        />
                    </div>

                </div>
            </div>

            <GeneralArticlesModal
                isUserWriter={isUserWriter}
                showGeneralArticlesModal={showGeneralArticlesModal}
                handleDiscardFilterChanges={handleDiscardFilterChanges}
                setShowGeneralArticlesModal={setShowGeneralArticlesModal}
                articleOwnershipFilters={articleOwnershipFilters}
                setArticleOwnershipFilters={setArticleOwnershipFilters}
                articleStatusFilters={articleStatusFilters}
                setArticleStatusFilters={setArticleStatusFilters}
                articleCategoryFilters={articleCategoryFilters}
                setArticleCategoryFilters={setArticleCategoryFilters}
                datePickerStartDate={datePickerStartDate}
                setDatePickerStartDate={setDatePickerStartDate}
                datePickerEndDate={datePickerEndDate}
                setDatePickerEndDate={setDatePickerEndDate}
                handleChangeGeneralArticlesFilters={handleChangeGeneralArticlesFilters}
            />
        </HeaderFooter>
    )
}

export default connect(matchStateToProps, matchDispatchToProps)(UserOffice)
