import React from 'react'
import { historyObject } from '../../../../historyObject';
import "./index.scss"

// test
export default function SimpleTemplate(props) {
    const { id, type = "paragraph", category = "News", title, author, date, children } = props;

    const upperCategory = category.toUpperCase()
    const typeClass = "simple-template-type-" + type

    const trimContent = (text) => {
        const size = 200
        return (text.length >= size ? text.slice(0, size + 1) + "..." : text)
    }


    return (
        <div className={typeClass} onClick={() => { historyObject.push(`/article/${id}`) }}>
            <div className={"simple-template-category"}>
                {upperCategory}
            </div>
            <div className="simple-template-title" > {/* `${36/(2**(Math.floor((title.length+35)/35)))}px` */}
                {title}
            </div>
            <div className="simple-template-content">
                {!!children && trimContent(children)}
            </div>
            <div className="simple-template-details">
                {author} | {date}
            </div>
        </div>
    )
}