import { createSlice } from "@reduxjs/toolkit";

//TODO
const initialState = {
    status: "idle",
    error: "",
    user: {
        _id: "0", 
        firstName: "edwin", //"Mmmmmmmmmmmmmmmmmmmmm"
        lastName: "li", //"Mmmmmmmmmmmmmmmmmmmmm"
        email: "edwin.li@outlook.com",
        role: "owner", 
        disabled: false,
        authenticated: true,
    }, 
    // user: null,
    credentials: {
        email: "",
        password: "",
    },
}

const slice = createSlice({
    initialState,
    name: "user",
    reducers: {
        changeUser: (state, action) => {
            state.user = action.payload
        },
        loadUser: (state, action) => {
            state.credentials.email = action.payload.email
            state.credentials.password = action.payload.password
            state.status = "LOADING"
        },
        loadUserSuccess: (state, action) => {
            state.credentials.email = ""
            state.credentials.password = ""
            state.status = "SUCCESS"
            state.user = action.payload
        },
        loadUserError: (state, action) => {
            state.credentials.email = ""
            state.credentials.password = ""
            state.status = "ERROR"
            state.error = action.payload
        },
    },
})


const { reducer, actions } = slice
export const {
    changeUser,
    loadUser, loadUserSuccess, loadUserError
} = actions
export default reducer