import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    status : "idle",
    error : "",
    filters : {
        ownership : ["All Articles", "My Articles"], //all articles, my articles
        status : ["editing", "pending", "rejected", "published"], //editing, pending, rejected, published
        category : ["news", "opinion", "sports", "entertainment", "clubs", "advertisement"], //
        date : {
            start : '', //Date.toString() will convert back to object later
            end : '',
        }
    },
    generalArticlesPerPage : 5,
    currentGeneralArticlePage : 1,
    totalGeneralArticles : 0, 
    generalArticles : [],
}

const slice = createSlice({ 
    initialState,
    name : "generalArticles",
    reducers : {
        changeCurrentGeneralArticlePage : (state, action) => {
            state.currentGeneralArticlePage = action.payload
        },
        changeGeneralArticlesPerPage : (state, action) => {
            state.generalArticlesPerPage = action.payload
        },
        changeGeneralArticlesFilters : (state, action) => {
            state.filters = action.payload
        },
        loadGeneralArticles : (state) => {
            state.status = "LOADING"
        },
        loadSuccess : (state, action) => {
            state.generalArticles = action.payload.generalArticles
            state.totalGeneralArticles = action.payload.totalGeneralArticles
            state.status = "SUCCESS"
        },
        loadError : (state, action) => {
            state.error = action.payload
            state.status = "ERROR"
        }
    },
})


const {reducer, actions} = slice
export const {changeCurrentGeneralArticlePage, changeGeneralArticlesPerPage, changeGeneralArticlesFilters, loadGeneralArticles, loadSuccess, loadError} = actions
export default reducer