import React from 'react'
import Button from '../../components/Button'
import Input from '../../components/Input'
import Modal from '../../components/Modal'
import "./index.scss"

export default function AccountSettingsModal(props) {
    const {
        showAccountSettingsModal,
        setShowAccountSettingsModal, 
        role,
        firstName,
        setAccountSettingsFirstName,
        lastName,
        setAccountSettingsLastName,
        email, 
        setAccountSettingsEmail,
        authenticated,
        temporaryPassword,
        accountSettingsRole,
        setAccountSettingsRole,
        setShowDeleteAccountModal,
        handleChangeAccountSettings
    } = props

    return (
        <Modal height={720} width={500} show={showAccountSettingsModal} handleShow={() => { setShowAccountSettingsModal(false) }}>
            <div className='account-settings-modal'>
                <div className='account-settings-modal title'>
                    <strong>{firstName} - {role.slice(0, 1).toUpperCase() + role.slice(1)}</strong>
                </div>
                <div className='account-settings-modal inputs'>
                    <div className='modal-inputs'>
                        First Name: <Input width="medium" defaultValue={firstName} onChange={setAccountSettingsFirstName} maxLength={20}>First Name</Input>
                    </div>
                    <div className='modal-inputs'>
                        Last Name: <Input width="medium" defaultValue={lastName} onChange={setAccountSettingsLastName} maxLength={50}>Last Name</Input>
                    </div>
                    <div className='modal-inputs'>
                        Email: <Input width="medium" defaultValue={email} onChange={setAccountSettingsEmail} maxLength={50}> Email Address</Input>
                    </div>
                </div>
                {
                    authenticated ? 
                        <></>
                        :
                        <div className='account-settings-modal temporary-password'>
                            Temporary Password:&nbsp;<strong>{temporaryPassword}</strong>
                        </div>
                }
                <div className='account-settings-modal role'>
                    <div className='role-selection-title'>
                        Role
                    </div>
                    <div className='role-selection-buttons'>
                        <div className={`role-selection-button ${accountSettingsRole === "writer" ? "selected" : "unselected"}`} onClick={() => {setAccountSettingsRole("writer")}}>
                            Writer
                        </div>
                        <div className={`role-selection-button ${accountSettingsRole === "editor" ? "selected" : "unselected"}`} onClick={() => {setAccountSettingsRole("editor")}}>
                            Editor
                        </div>
                        <div className={`role-selection-button ${accountSettingsRole === "owner" ? "selected" : "unselected"}`} onClick={() => {setAccountSettingsRole("owner")}}>
                            Owner
                        </div>
                    </div>
                </div>
                <div className='account-settings-modal delete-account'>
                    <Button color="red" onClick={() => {setShowDeleteAccountModal(true);}}>
                        Delete Account
                    </Button>
                </div>
                <div className='account-settings-modal controls'>
                    <Button color="red" onClick={() => {setShowAccountSettingsModal(false);}}>
                        Cancel
                    </Button>
                    <Button color="green" onClick={() => {handleChangeAccountSettings(); setShowAccountSettingsModal(false); }}>
                        Save
                    </Button>
                </div>
            </div>
        </Modal>
        )
}
