import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../services/api";
import { loadUserError, loadUserSuccess } from ".";
import { getCredentials } from "./selector";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* loginUserSaga() {
    try {
        const { email, password } = yield select(getCredentials)
        const { token, ...rest } = yield call([api, api.loginUser], email, password)
        if (rest.disabled){
            localStorage.clear("token")
        } else{
            localStorage.setItem("token", token) // saves jwt token to local storage
        }
        yield put(loadUserSuccess(rest))
    } catch (e) {
        yield put(loadUserError(getResponseError(e)))
        console.log("Error from Login in Saga: ", e)
    }
}