import React from 'react'
import { historyObject } from '../../../historyObject';
import Button from '../../components/Button';
import './index.scss'
import logoImage from '../../../assets/images/foothillLogo.png';
import { getUserName, getUserId, isLoggedIn } from '../../../store/reducer/login/user/selector';
import { changeUser } from '../../../store/reducer/login/user/index'
import { connect } from 'react-redux';
import ProfileDropdown from '../../components/ProfileDropdown';

const mapStateToProps = (state) => ({
    isLoggedIn: isLoggedIn(state),
    userName: getUserName(state),
    userId: getUserId(state),
})

const mapDispatchToProps = { changeUser }

function HeaderFooter(props) {

    const { children, title, isLoggedIn, } = props; // title = "normal", "none", "left", "right"
    const TITLE = "The Foothill Herald"

    // Home Details
    const getDate = () => {
        let date = new Date()

        let month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][date.getMonth()]
        let day = date.getDate();
        let year = date.getFullYear();

        return month + ". " + day + ", " + year; // local because we don't really use it anywhere else
    }

    const goToHome = () => {
        historyObject.push("/")
    }

    const goToAboutUs = () => {
        historyObject.push("/aboutus")
    }

    const goToContactUs = () => {
        historyObject.push("/contactus")
    }

    const goToLogin = () => {
        historyObject.push("/login")
    }


    return (
        <div className="header-footer">
            <div className="header">
                <div className="the-foothill-herald" onClick={goToHome}>
                    {TITLE}
                </div>
                <div className="links">
                    <Button onClick={goToAboutUs} type={"link"}>
                        About Us
                    </Button>
                    <Button onClick={goToContactUs} type={"link"}>
                        Contact Us
                    </Button>
                    {
                        isLoggedIn ?
                            <></>
                            :
                            <Button onClick={goToLogin} type={"link"}>
                                Login
                            </Button>
                    }
                </div>
            </div>

            {title !== "none" &&
                <div className={"header-title " + title}>
                    <div className='header-title-left'>
                        <div className="logo-frame">
                            <img className="logo" src={logoImage} alt="logo" />
                        </div>
                        <div className="the-foothill-herald">
                            {TITLE}
                        </div>
                        <div className="date">
                            {getDate()}
                        </div>
                    </div>


                    <div className='header-title-right'>
                        <ProfileDropdown />
                    </div>
                </div>
            }

            <div className="header-footer-children">
                {children}
            </div>
            {/* {children} */}

            <footer className="footer">
                <div className="left-side">
                    <img className="footer-logo" src={logoImage} alt="logo" />
                    <div className="footer-title">
                        <div className="the-foothill-herald">
                            {TITLE}
                        </div>
                        <div className="footer-links">
                            <Button type="minilink" onClick={goToLogin}>
                                Login
                            </Button>
                            &nbsp;|&nbsp;
                            <Button type="minilink" onClick={goToAboutUs}>
                                About Us
                            </Button>
                            &nbsp;|&nbsp;
                            <Button type="minilink" onClick={goToContactUs}>
                                Contact Us
                            </Button>
                        </div>
                    </div>
                </div>

                <div className="social-medias">

                </div>

            </footer>
        </div>
    )
}

export default connect(mapStateToProps, mapDispatchToProps)(HeaderFooter)
