import React from 'react';
import { historyObject } from '../../../../historyObject';
import "./index.scss";

export default function ImageTemplate(props) {
    const { id, image = "", category = "News", title, author, date } = props;

    const upperCategory = category.toUpperCase()

    return (
        <div className="image-template" onClick={() => { historyObject.push(`/article/${id}`) }}>
            <img className="image" src={image != null ? image : ""} alt={title} />
            <div className="image-template-content">
                <div className="image-template-category">
                    {upperCategory}
                </div>
                <div className="image-template-title">
                    {title}
                </div>
                <div className="image-template-details">
                    {author} | {date}
                </div>
            </div>
        </div>
    )
}
