import { call, put, select } from "redux-saga/effects";
import * as api from "../../../services/api";
import { changeHomepagePreviewArticlesData, loadError } from ".";
import { getHomepageCurrentPreviewArticlesFilter, getHomepagePreviewArticlesData } from "./selector";

const getResponseError = (e) => {
    if (e.response && e.response.data){
        return e.response.data
    }
    return {
        status : 500,
        type : "unkown-error",
    }
    
}

export function* previewArticlesSaga(){
    try {
        const { previewArticlesPerPage, currentHomePage } = yield select(getHomepagePreviewArticlesData)
        const filter = yield select(getHomepageCurrentPreviewArticlesFilter)
        const currentPreviewArticles = yield call([api, api.getHomepagePreviewArticlesData], previewArticlesPerPage, currentHomePage, filter)
        yield put(changeHomepagePreviewArticlesData(currentPreviewArticles))
    } catch(e){
        yield put(loadError(getResponseError(e)))
    }
}

