export const getArticleImages = (state) => {
    return state.articleEditingPlatform.imagesEditor.imgs
}

export const isLoadImagesEditorSuccess = (state) => {
    return state.articleEditingPlatform.imagesEditor.status === "SUCCESS"
}

export const isLoadImagesEditorError = (state) => {
    return state.articleEditingPlatform.imagesEditor.status === "ERROR"
}