import React from 'react'
import "./index.scss"
import SimpleTemplate from '../SimpleTemplate';
import { historyObject } from '../../../../historyObject';

export default function CompositeTemplate(props) {
    const { id, image, category = "News", title, author, date, children } = props;

    return (
        <div className="composite-template" onClick={() => { historyObject.push(`/article/${id}`) }}>
            <img className="composite-template-image" src={image} alt={title} />
            <SimpleTemplate type="long" category={category} title={title} author={author} date={date}>
                {children}
            </SimpleTemplate>
        </div>
    )
}
