import React from 'react'
import Button from '../Button'
import "./index.scss"

export default function Dropdown(props) {
    const { values=[], name, currValue, setValue, button=false, buttonLabel, onClick, disabled=false, color="grey", border="default"} = props

    const handleDropdownChange = (e) => {
        setValue(e.target.value)
    }

    const handleButtonClick = () => {
        onClick()
    }

    const dropdownClasses = [
        "foothill-dropdown",
        "color-" + color, //grey, blue
        "border-" + border, //default, thin, thick
    ].join(" ")

    const dropdownValuesClasses = [
        "foothill-dropdown-values",  
        "color-" + color, //grey, blue
        "border-" + border, //default, thin, thick
    ].join(" ")

    return (
        <>
            <select className = {dropdownClasses} name={name} value={currValue} onChange={handleDropdownChange} disabled={disabled}>
                {values.map((value) => 
                    <option className = {dropdownValuesClasses} key={value} value={value}>{value}</option>
                )}
            </select>
            {
                button && 
                <Button type="submit" onClick={handleButtonClick}>
                    {buttonLabel}
                </Button>
            }
            
        </>

    )
}
