import { takeLatest } from "redux-saga/effects";
import { loadHomepagePreviewArticles } from "../reducer/previewArticles";
import { previewArticlesSaga } from "../reducer/previewArticles/saga";
import { loadDeleteEditingArticle, loadEditingArticle, loadPublishEditingArticle, loadPublishPendingArticle, loadRejectPendingArticle, loadUpdateEditingArticle } from "../reducer/articleEditingPlatform/articleEditor";
import { deleteEditingArticleSaga, loadEditingArticleSaga, publishEditingArticleSaga, publishPendingArticleSaga, rejectPendingArticleSaga, updateEditingArticleSaga } from "../reducer/articleEditingPlatform/articleEditor/saga";
import { viewArticleSaga } from "../reducer/viewArticle/saga";
import { loadCurrViewingArticle } from "../reducer/viewArticle";
import { loadPendingArticles } from "../reducer/userOffice/pendingArticles";
import { pendingArticlesSaga } from "../reducer/userOffice/pendingArticles/saga";
import { loadGeneralArticles } from "../reducer/userOffice/generalArticles";
import { generalArticlesSaga } from "../reducer/userOffice/generalArticles/saga";
import { loadUser } from "../reducer/login/user";
import { loginUserSaga } from "../reducer/login/user/saga";
import { loadAuthenticationPassword } from "../reducer/login/authentication";
import { loginAuthenticationSaga } from "../reducer/login/authentication/saga";
import { loadUserAccounts } from "../reducer/userProfile/ownerProfile/userAccounts";
import { loadAddAccount } from "../reducer/userProfile/ownerProfile/addUserAccount";
import { loadDeleteAccount, loadUpdateAccount } from "../reducer/userProfile/ownerProfile/currUserAccountSettings";
import { loadChangePassword } from "../reducer/userProfile/generalProfile/changePassword";
import { loadUpdateUserProfile } from "../reducer/userProfile/generalProfile/updateUserProfile";
import { ownerProfileUserAccountsSaga } from "../reducer/userProfile/ownerProfile/userAccounts/saga";
import { ownerProfileAddAccountSaga } from "../reducer/userProfile/ownerProfile/addUserAccount/saga";
import { ownerProfileAccountSettingsSaga, ownerProfileDeleteAccountSaga } from "../reducer/userProfile/ownerProfile/currUserAccountSettings/saga";
import { userProfileChangePasswordSaga } from "../reducer/userProfile/generalProfile/changePassword/saga";
import { userProfileUpdateUserProfileSaga } from "../reducer/userProfile/generalProfile/updateUserProfile/saga";
import { loadRefreshToken } from "../reducer/login/refreshToken";
import { refreshTokenSaga } from "../reducer/login/refreshToken/saga";
import { loadCreateNewArticle } from "../reducer/userOffice/createNewArticle";
import { createNewArticleSaga } from "../reducer/userOffice/createNewArticle/saga";
import { loadImagesEditor } from "../reducer/articleEditingPlatform/imagesEditor";
import { imagesEditorSaga } from "../reducer/articleEditingPlatform/imagesEditor/saga";


/* EXPORT YOUR SAGAS HERE */
export function* sagas(){
    yield takeLatest([loadUser], loginUserSaga)
    yield takeLatest([loadAuthenticationPassword], loginAuthenticationSaga)
    yield takeLatest([loadHomepagePreviewArticles], previewArticlesSaga)

    yield takeLatest([loadEditingArticle], loadEditingArticleSaga)
    yield takeLatest([loadUpdateEditingArticle], updateEditingArticleSaga)
    yield takeLatest([loadDeleteEditingArticle], deleteEditingArticleSaga)
    yield takeLatest([loadPublishEditingArticle], publishEditingArticleSaga)
    yield takeLatest([loadPublishPendingArticle], publishPendingArticleSaga)
    yield takeLatest([loadRejectPendingArticle], rejectPendingArticleSaga)
    yield takeLatest([loadImagesEditor], imagesEditorSaga)

    yield takeLatest([loadPendingArticles], pendingArticlesSaga)
    yield takeLatest([loadGeneralArticles], generalArticlesSaga)
    yield takeLatest([loadCurrViewingArticle], viewArticleSaga)
    yield takeLatest([loadUserAccounts], ownerProfileUserAccountsSaga)
    yield takeLatest([loadAddAccount], ownerProfileAddAccountSaga)
    yield takeLatest([loadUpdateAccount], ownerProfileAccountSettingsSaga)
    yield takeLatest([loadDeleteAccount], ownerProfileDeleteAccountSaga)
    yield takeLatest([loadChangePassword], userProfileChangePasswordSaga)
    yield takeLatest([loadUpdateUserProfile], userProfileUpdateUserProfileSaga)
    yield takeLatest([loadRefreshToken], refreshTokenSaga)
    yield takeLatest([loadCreateNewArticle], createNewArticleSaga)

}