import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../../services/api";
import { loadAddAccountSuccess, loadAddAccountError } from ".";
import { getAddAccount } from "./selector";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* ownerProfileAddAccountSaga() {
    try {
        const account = yield select(getAddAccount)
        const data = yield call([api, api.registerUser], account)
        yield put(loadAddAccountSuccess(data))
    } catch (e){
        yield put(loadAddAccountError(getResponseError(e)))
        console.log("Error from Owner Profile Saga (Add Account): ", e)
    }
}

