import React, { useEffect } from 'react'
import Dropdown from '../../../components/Dropdown'
import RadioButtons from '../../../components/RadioButtons'
import "./index.scss"
import UploadImage from './UploadImage'
import Moment from 'react-moment'
export default function ArticleEditorSettings(props) {
    const {
        status,
        isAuthor,
        editingArticleCategory,
        editingArticleTemplate,
        editingArticleImages,
        editingArticleWordCount,

        saved,
        serverArticle,
        editingArticleSavedDate,

        setEditingArticleCategory,
        setEditingArticleTemplate,
        setEditingArticleImages,
        setEditingArticleWordCount,

        loadUpdateEditingArticle,
    } = props

    const templates = ["none", "top", "middle", "bottom", "left", "right", "top-bottom"]
    const categories = ["NEWS", "OPINION", "SPORTS", "ENTERTAINMENT", "CLUBS", "ADVERTISING"]

    useEffect(() => {
        setEditingArticleWordCount(serverArticle.wordcount)
    }, [setEditingArticleWordCount, serverArticle.wordcount])

    const handleArticleTemplateChange = async (newTemplate) => {
        setEditingArticleTemplate(newTemplate)
        
    }


    return (
        <div className="article-editor-settings">
            Article Settings
            <div className="templates-form">
                Image Orientation
                <RadioButtons
                    values={templates}
                    name={"templates"}
                    currValue={editingArticleTemplate}
                    setValue={handleArticleTemplateChange}
                    disabled={status === "pending" || status === "published" || !isAuthor}
                />

            </div>
            <div className="category">
                Category
                <Dropdown
                    currValue={editingArticleCategory}
                    values={categories}
                    setValue={setEditingArticleCategory}
                    disabled={status === "pending" || status === "published" || !isAuthor}
                />
            </div>
            <div className="details">
                <div>
                    Article is {saved ? "SAVED" : "NOT SAVED"}
                </div>
                <div>
                    Last saved &nbsp;
                    <strong>
                        <Moment interval={10000} fromNow>
                            {editingArticleSavedDate}
                        </Moment>
                    </strong>
                </div>
                <div>
                    Word Count:&nbsp; <strong>{editingArticleWordCount}</strong>
                </div>
            </div>
            {/* TODO Image uploading to store and database*/}
            <div className="upload-image">
                <UploadImage
                    editingArticleImages={editingArticleImages}
                    setEditingArticleImages={setEditingArticleImages}
                    editingArticleTemplate={editingArticleTemplate}
                    loadUpdateEditingArticle={loadUpdateEditingArticle}
                    disabled={status === "pending" || status === "published" || !isAuthor}
                />
            </div>

        </div>
    )
}

