import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../services/api";
import { loadSuccess, loadError } from ".";
import { getUserId, getUserRole } from "../../login/user/selector";
import { getCurrentPendingArticlesPage, getPendingArticlesPerPage } from "./selector";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* pendingArticlesSaga() {
    try {
        const userId = yield select(getUserId)
        const userRole = yield select(getUserRole)
        const pendingArticlesPerPage = yield select(getPendingArticlesPerPage)
        const currentPendingArticlePage = yield select(getCurrentPendingArticlesPage)
        const {pendingArticles, totalPendingArticles} = yield call([api, api.getPendingArticlesData], userId, userRole, pendingArticlesPerPage, currentPendingArticlePage) 
        yield put(loadSuccess({pendingArticles, totalPendingArticles}))
    } catch (e) {
        yield put(loadError(getResponseError(e)))
        console.log("Error from User Office Saga: ", e)
    }
}