import React from 'react'
import "./index.scss"


export default function Button(props) {
    const { children, type = "link", color = "blue", border = "default", size = "default", onClick = () => { }, disabled = false, corners = "default"} = props; //type, color, border, size, onClick, children

    const handleClick = () => {
        onClick()
    }

    const classes = [
        "foothill-btn",
        "color-" + color, //blue, yellow, green, red, 
        "border-" + border, //default, thin, thick
        "button-" + type, // link, minilink, login, navbar
        "size-" + size, // default, long, small
        "corners-" + corners, // default, rounded, squared

    ].join(" ")

    return (
        <>
            {
                disabled ?
                    <button className={classes} onClick={handleClick} disabled>{children}</button>
                    :
                    <button className={classes} onClick={handleClick}>{children}</button>
            }
        </>
    )
}





