import { call, put, select } from "redux-saga/effects";
import { loadError, loadSuccess } from ".";
import { getUser } from "../../login/user/selector";
import * as api from "../../../../services/api";
import { historyObject } from "../../../../historyObject";
import { changeCurrEditingArticleId } from "../../articleEditingPlatform/articleEditor";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* createNewArticleSaga() { 
    try {
        const user = yield select(getUser)
        const newArticleId = yield call([api, api.createNewArticle], user)
        console.log("newArticleId", newArticleId)
        yield put(changeCurrEditingArticleId(newArticleId))
        yield put(loadSuccess())
        historyObject.push(`/articleeditingplatform?id=${newArticleId}`)
    } catch (e) {
        yield put(loadError(getResponseError(e)))
    }
}