import React, { useEffect, useRef, useState } from 'react'
import "./index.scss"
import { faPalette } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { EditorState, Modifier, RichUtils } from 'draft-js'
import StyleButton from '../StyleButton'

// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

export default function ColorPicker(props) {

    const { editorState, setEditorState } = props


    const COLORS = [
        { label: <span style={{ color: "red" }}>Red</span>, style: "red" },
        { label: <span style={{ color: "green" }}>Green</span>, style: "green" },
        { label: <span style={{ color: "black" }}>Black</span>, style: "black" },
    ]

    // Color Pallete Dropdown Control

    const [isColorPaletteVisible, setIsColorPaletteVisible] = useState(false);

    const colorPaletteRef = useRef(null);

    const handleHideDropdown = (event) => {
        if (event.key === "Escape") {
            setIsColorPaletteVisible(false)
        }
    }

    const handleClickOutside = (event) => {
        if (colorPaletteRef.current && !colorPaletteRef.current.contains(event.target)) {
            setIsColorPaletteVisible(false)
        }
    }

    useEffect(() => {
        document.addEventListener("keydown", handleHideDropdown, true)
        document.addEventListener("click", handleClickOutside, true)
    })


    // Controlling Editor Color
    const currentStyle = editorState.getCurrentInlineStyle()

    const currentStyleContainsColor = () => {
        for (const color of COLORS) {
            if (currentStyle.has(color.style)) {
                return true
            }
        }
        return false
    }

    const toggleColor = (toggledColor) => {
        const selection = editorState.getSelection();

        // Let's just allow one color at a time. Turn off all active colors.
        const nextContentState = Object.keys(colorStyleMap).reduce((contentState, color) => {
            return Modifier.removeInlineStyle(contentState, selection, color)
        }, editorState.getCurrentContent());

        let nextEditorState = EditorState.push(
            editorState,
            nextContentState,
            'change-inline-style'
        );

        const currentStyle = editorState.getCurrentInlineStyle();

        // Unset style override for current color.
        if (selection.isCollapsed()) {
            nextEditorState = currentStyle.reduce((state, color) => {
                return RichUtils.toggleInlineStyle(state, color);
            }, nextEditorState);
        }

        // If the color is being toggled on, apply it.
        if (!currentStyle.has(toggledColor)) {
            nextEditorState = RichUtils.toggleInlineStyle(
                nextEditorState,
                toggledColor
            );
        }

        setEditorState(nextEditorState);
    }

    return (
        <div ref={colorPaletteRef} className="color-picker" >
            <StyleButton
                active={currentStyleContainsColor()}
                onClick={() => { setIsColorPaletteVisible(!isColorPaletteVisible) }}
            >
                <FontAwesomeIcon icon={faPalette} />
            </StyleButton>
            {isColorPaletteVisible &&
                <div className={"color-palette"}>
                    {
                        COLORS.map((color) => {
                            return <StyleButton
                                key={color.style}
                                active={currentStyle.has(color.style)}
                                onClick={() => { toggleColor(color.style) }}
                            >
                                {color.label}
                            </StyleButton>
                        }) 
                    }
                </div>


            }
        </div>


    )
}



export const colorStyleMap = {
    red: {
        color: 'rgba(255, 0, 0, 1.0)',
    },
    orange: {
        color: 'rgba(255, 127, 0, 1.0)',
    },
    yellow: {
        color: 'rgba(180, 180, 0, 1.0)',
    },
    green: {
        color: 'rgba(0, 180, 0, 1.0)',
    },
    blue: {
        color: 'rgba(0, 0, 255, 1.0)',
    },
    indigo: {
        color: 'rgba(75, 0, 130, 1.0)',
    },
    violet: {
        color: 'rgba(127, 0, 255, 1.0)',
    },
}
