import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import { Provider } from "react-redux";
import { configureStore, getDefaultMiddleware } from "@reduxjs/toolkit";
import createSagaMiddleware from "redux-saga";
import { sagas } from "./store/saga";
import reducer from "./store/reducer";
import { ConnectedRouter } from "connected-react-router";
import Router from "./features/router";
import { historyObject } from './historyObject';
import { worker } from './mocks/browser';

/* istanbul ignore file */
const devMode = process.env.DEBUG === "true" || process.env.NODE_ENV === "development";
const sagaMiddleware = createSagaMiddleware();
const middleware = [...getDefaultMiddleware({ thunk: false }), sagaMiddleware]; // serializableCheck: false ???

if (process.env.NODE_ENV === "development") {
  worker.start({ onUnhandledRequest: "bypass" })
}

const store = configureStore({
  reducer: reducer,
  devTools: devMode,
  middleware
});

sagaMiddleware["run"](sagas);

if (devMode && module["hot"]) {
  module["hot"].accept("./store/reducer/index.js", () => store.replaceReducer(reducer));
}



/*instanbul ignore next */
ReactDOM.render(
  <Provider store={store}>
    <ConnectedRouter history={historyObject}>
      <Router />
    </ConnectedRouter>
  </Provider>, document.getElementById('root'));
