import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    status : "idle",
    error : "",
    homepagePreviewArticlesData : {
        currentHomePage : 1,
        previewArticlesPerPage : 10,
        previewArticles : [], //current articles
        totalPreviewArticles : 0, //total number of articles
        filter : ""
    },

}

const slice = createSlice({
    initialState,
    name : "previewArticles",
    reducers : {
        changeHomepagePreviewArticlesData : (state, action) => {
            state.homepagePreviewArticlesData.previewArticles = action.payload.previewArticles
            state.homepagePreviewArticlesData.totalPreviewArticles = action.payload.totalPreviewArticles
        },
        changeCurrentHomePage : (state, action) => {
            state.homepagePreviewArticlesData.currentHomePage = action.payload
        },
        changePreviewArticlesPerPage : (state, action) => {
            state.homepagePreviewArticlesData.previewArticlesPerPage = action.payload
        },
        changePreviewArticlesFilter : (state, action) => {
            state.homepagePreviewArticlesData.filter = action.payload
        },
        loadHomepagePreviewArticles : (state) => {
            state.status = "LOADING"
        },

        loadSuccess : (state) => {
            state.status = "SUCCESS"
        },

        loadError : (state, action) => {
            state.status = "ERROR"
            state.error = action.payload
        }


    },
})


const {reducer, actions} = slice
export const {changeHomepagePreviewArticlesData, changeCurrentHomePage, changePreviewArticlesPerPage, changePreviewArticlesFilter, loadHomepagePreviewArticles, loadSuccess, loadError} = actions
export default reducer