import React, { useEffect } from 'react'
// import { useLocation } from 'react-router'
import './index.scss'
import HeaderFooter from '../../layout/headerfooter'
import { loadCurrViewingArticle } from '../../../store/reducer/viewArticle/index'
import { getCurrViewingArticle } from '../../../store/reducer/viewArticle/selector'
import { connect } from 'react-redux'
import parse from "html-react-parser"
import { historyObject } from '../../../historyObject'
import Button from '../../components/Button'


const matchStateToProps = (state) => ({
  currViewingArticle: getCurrViewingArticle(state),
})

const mapDispatchToProps = { loadCurrViewingArticle }

function ViewArticle(props) {
  const { currViewingArticle, match: { params: { id } }, loadCurrViewingArticle } = props

  const handleReturnToArticleEditingPlatform = () => {
    historyObject.push(`/articleeditingplatform?articleId=${id}`)
  }

  useEffect(() => {
    loadCurrViewingArticle(id)
  }, [id, loadCurrViewingArticle])

  console.log(currViewingArticle)
  console.log(currViewingArticle.status)

  return (
    <HeaderFooter title="middle">
      <div className="view-article">
        <div className="view-article-header">
          <div className="view-article-category">
            {currViewingArticle.category.toUpperCase()}
          </div>
          <div className="view-article-title">
            {currViewingArticle.title}
          </div>
          <div className="view-article-author-date">
            {currViewingArticle.author} | {currViewingArticle.date}
          </div>
        </div>
        <div className="view-article-content">
          <div className="view-article-article">
            {parse(`${currViewingArticle.content}`)}
          </div>
          <div className="view-article-recommendations">
          </div>
        </div>
        {
          currViewingArticle.status !== "published" && 
          <div>
            <Button onClick={handleReturnToArticleEditingPlatform}>
              Return to Editing Platform
            </Button>
          </div>
        }
      </div>
    </HeaderFooter>
  )
}

export default connect(matchStateToProps, mapDispatchToProps)(ViewArticle)
