import React, { useEffect, useState } from 'react'
import Editor from '@draft-js-plugins/editor'
import { EditorState, /*Modifier,*/ RichUtils } from 'draft-js'
import { stateToHTML } from 'draft-js-export-html';
import Button from '../../../components/Button';
import EditingToolbar from './EditingToolbar';
import Input from '../../../components/Input';
import addLinkPlugin from './EditingToolbar/EditorLinkPlugin';
import { colorStyleMap } from './EditingToolbar/ColorPicker';
import { stateFromHTML } from 'draft-js-import-html';
import "./index.scss";

export default function ArticleEditor(props) {
    const {
        status, userRole, isAuthor,
        setEditingArticleTitle,
        setEditingArticleContent,
        setEditingArticleWordCount,

        serverArticle, // ONLY INCLUDES TITLE AND CONTENT

        loadUpdateEditingArticle,
        loadDeleteEditingArticle,
        loadPublishEditingArticle,
        loadPublishPendingArticle,
        loadRejectPendingArticle,
        handlePreviewEditingArticle,
    } = props


    const [editorState, setEditorState] = useState(
        EditorState.createWithContent(stateFromHTML(serverArticle.content))
    )

    const getWordCount = (editorState) => {
        const plainText = editorState.getCurrentContent().getPlainText('');
        const regex = /(?:\r\n|\r|\n)/g;  // new line, carriage return, line feed
        const cleanString = plainText.replace(regex, ' ').trim(); // replace above characters w/ space
        const wordArray = cleanString.match(/\S+/g);  // matches words according to whitespace
        return wordArray ? wordArray.length : 0;
    }

    const handleEditorChange = (newEditorState) => {
        setEditorState(newEditorState)
        setEditingArticleContent(stateToHTML(newEditorState.getCurrentContent()))
        setEditingArticleWordCount(getWordCount(newEditorState))
    }

    const handleKeyCommand = (command, editorState) => { // Stuff like ctrl+B, ctrl+U, ctrl+I
        const newState = RichUtils.handleKeyCommand(editorState, command);

        if (newState) {
            setEditorState(newState);
            return 'handled';
        }

        return 'not-handled';
    }

    useEffect(() => {
        setEditorState(EditorState.createWithContent(stateFromHTML(serverArticle.content)))
    }, [serverArticle.content])

    return (
        <div className="article-editor-background">
            <div className='article-editor{'>
                <div className="article-editor-title-editor">
                    <Input disabled={status === "pending" || status === "published" || !isAuthor} defaultValue={serverArticle.title} onChange={setEditingArticleTitle} width={"full"} fontSize={"large"} background={"beige"} rounded={false} maxLength={"75"}>
                        Title
                    </Input>
                </div>
                <EditingToolbar
                    editorState={editorState}
                    setEditorState={setEditorState}
                />
                <Editor
                    readOnly={status === "pending" || status === "published" || !isAuthor}
                    spellCheck={true}
                    editorState={editorState}
                    onChange={handleEditorChange}
                    handleKeyCommand={handleKeyCommand}
                    customStyleMap={colorStyleMap}
                    plugins={[addLinkPlugin]}
                />
            </div>
            <div className="article-editor-buttons">
                {
                    (status === "editing" || status === "rejected") && isAuthor &&
                    <>
                        
                        <div className="article-editor-button">
                            <Button onClick={loadUpdateEditingArticle}>
                                Save
                            </Button>
                        </div>
                        <div className="article-editor-button">
                            <Button className="article-editor-button" onClick={loadDeleteEditingArticle}>
                                Discard
                            </Button>
                        </div>
                        <div className="article-editor-button">
                            <Button className="article-editor-button" onClick={loadPublishEditingArticle}>
                                Request to Publish
                            </Button>
                        </div>
                    </>
                }
                {
                    (status === "pending") && ((userRole === "owner") || ((userRole === "editor") && !isAuthor)) &&
                    <>
                        <div className="article-editor-button">
                            <Button className="article-editor-button" onClick={loadRejectPendingArticle}>
                                Reject
                            </Button>
                        </div>
                        <div className="article-editor-button">
                            <Button className="article-editor-button" onClick={loadPublishPendingArticle}>
                                Confirm Publish
                            </Button>
                        </div>
                    </>
                }
                <div className='article-editor-button'>
                    <Button className="article-editor-button" onClick={handlePreviewEditingArticle}>
                        Preview
                    </Button>
                </div>
            </div>

        </div>
    )
}