import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../../services/api";
import { loadChangePasswordError, loadChangePasswordSuccess } from ".";
import { getUserChangePassword } from "./selector";
import { getUserId } from "../../../login/user/selector";

const getResponseError = (e) => {
    if (e.response && e.response.data){
        return e.response.data
    }
    return {
        status : 500,
        type : "unkown-error",
    }
    
}

export function* userProfileChangePasswordSaga(){
    try {
        const _id = yield select(getUserId)
        const {oldPassword, newPassword, confirmNewPassword} = yield select(getUserChangePassword)
        yield call([api, api.updateUserPassword], _id, oldPassword, newPassword, confirmNewPassword)
        yield put(loadChangePasswordSuccess())
    } catch(e){
        yield put(loadChangePasswordError(getResponseError(e)))
        console.log("Error from User Profile Change Password Saga: ", e)
    }
}