import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    status : "idle",
    error : "",
    pendingArticlesPerPage: 5,
    pendingArticles : [],
    totalPendingArticles : 0,
    currentPendingArticlePage : 1,

}

const slice = createSlice({
    initialState,
    name : "pendingArticles",
    reducers : {
        changeCurrentPendingArticlePage : (state, action) => {
            state.currentPendingArticlePage = action.payload
        },
        changePendingArticlesPerPage : (state, action) => {
            state.pendingArticlesPerPage = action.payload
        },
        loadPendingArticles : (state) => {
            state.status = "LOADING"
        },
        loadSuccess : (state, action) => {
            state.status = "SUCCESS"
            state.pendingArticles = action.payload.pendingArticles
            state.totalPendingArticles = action.payload.totalPendingArticles
        },
        loadError : (state) => {
            state.status = "ERROR"
        }
    },
})


const {reducer, actions} = slice
export const {changeCurrentPendingArticlePage, changePendingArticlesPerPage, loadPendingArticles, loadSuccess, loadError} = actions
export default reducer