import { createSlice } from "@reduxjs/toolkit";

//TODO
const initialState = {
    status: "idle",
    error: "",
}

const slice = createSlice({
    initialState,
    name: "refreshToken",
    reducers: {
        loadRefreshToken: (state) => {
            state.status = "LOADING"
        },
        loadRefreshTokenSuccess: (state) => {
            state.status = "SUCCESS"
        },
        loadRefreshTokenError: (state) => {
            state.status = "ERROR"
        },
    },
})


const { reducer, actions } = slice
export const {
    loadRefreshToken, loadRefreshTokenSuccess, loadRefreshTokenError
} = actions
export default reducer