import React from 'react'
import "./index.scss"

export default function UploadFile(props) {
    const { children, onChange = () => { }, disabled=true, accept = "image/*", background = "blue", corners = "rounded" } = props

    const handleOnChange = (e) => {
        console.log()
        onChange(e)
    }

    const classes = [
        "upload-file",
        "background-" + background, // blue
        "corners-" + corners, // default, rounded, squared
    ].join(" ")

    // width is 100% of parent container, alter that to change width of input



    return (
        <>
            <label className={classes} htmlFor={`file-upload-id-${children}`}>
                {children}
            </label>
            <input type='file' onChange={handleOnChange} id={`file-upload-id-${children}`} disabled={disabled} style={{ display: "none" }} accept={accept} />
        </>
    )

}