import React from 'react'
import ReactDatePicker from 'react-datepicker';
import Button from '../../components/Button';
import CheckBoxes from '../../components/CheckBoxes';
import Modal from '../../components/Modal';
import "./index.scss"

export default function GeneralArticlesModal(props) {
    const {
        isUserWriter,
        showGeneralArticlesModal,
        handleDiscardFilterChanges,
        setShowGeneralArticlesModal,
        articleOwnershipFilters,
        setArticleOwnershipFilters,
        articleStatusFilters,
        setArticleStatusFilters,
        articleCategoryFilters,
        setArticleCategoryFilters,
        datePickerStartDate,
        setDatePickerStartDate,
        datePickerEndDate,
        setDatePickerEndDate,
        handleChangeGeneralArticlesFilters,
    } = props
    return (
        <Modal height={600} width={500} show={showGeneralArticlesModal} handleShow={() => {handleDiscardFilterChanges(); setShowGeneralArticlesModal(false);}}>
            <div className='general-articles-modal'>
                <div className='general-articles-modal title'>
                    Article Filters
                </div>
                {
                    !isUserWriter && 
                    <div className='general-articles-modal-ownership'>
                        <CheckBoxes
                            values={["All Articles", "My Articles"]} 
                            currValueList={articleOwnershipFilters}
                            setValueList={setArticleOwnershipFilters}
                        />
                    </div>
                }
                <div className='general-articles-modal-status-category'>
                    <div className='general-articles-modal-status'>
                        Status
                        <CheckBoxes
                            values={["editing", "pending", "rejected", "published"]} 
                            currValueList={articleStatusFilters}
                            setValueList={setArticleStatusFilters}
                        />
                    </div>
                    <div className='general-articles-modal-category'>
                        Category
                        <CheckBoxes
                            values={["news", "opinion", "sports", "entertainment", "clubs", "advertisement"]} 
                            currValueList={articleCategoryFilters}
                            setValueList={setArticleCategoryFilters}
                        />
                    </div>
                </div>
                <div className='general-articles-modal-dates'>
                    <div className='general-articles-modal-dates start'>
                        Start Date <ReactDatePicker selected={datePickerStartDate} onChange={(date) => {setDatePickerStartDate(date)}}/>
                    </div>
                    <div className='general-articles-modal-dates end'>
                        End Date <ReactDatePicker selected={datePickerEndDate} onChange={(date) => {setDatePickerEndDate(date)}}/>
                    </div>
                </div>
                <div className="general-articles-modal-decisions">
                    <Button color={"red"} type={"decision"} onClick={() => {handleDiscardFilterChanges(); setShowGeneralArticlesModal(false);}}>
                        Discard
                    </Button>
                    <Button color={"green"} type={"decision"} onClick={() => {handleChangeGeneralArticlesFilters(); setShowGeneralArticlesModal(false);}}>
                        Save
                    </Button>
                </div>
            </div>
        </Modal>
    )
}
