//TODO store in dotenv
const REACT_APP_CLOUD_NAME = 'dir4cujs4'
const REACT_APP_PRESET_NAME = 'ykr0zcfj'
const REACT_APP_API_KEY = '225265636819618'
const REACT_APP_API_SECRET = 'ogAIFon2PSyGn6w1r8FiB0W5QhE'

cloudinary.config({
    cloud_name: REACT_APP_CLOUD_NAME,
    api_key: REACT_APP_API_KEY,
    api_secret: REACT_APP_API_SECRET,
});


export default function UploadImage(props) {
    const { editingArticleImages, setEditingArticleImages,
        editingArticleTemplate,
        loadUpdateEditingArticle,
        disabled,
    } = props

    const [numImagesToUpload, setNumImagesToUpload] = useState(1)

    const addImage = (e) => {
        if (editingArticleImages.length < numImagesToUpload) {
            if (e.target.files[0]) {
                let imageFile = e.target.files[0]
                var reader = new FileReader()
                reader.onload = (e) => {
                    var img = document.createElement("img")
                    img.onload = (event) => {
                        const MAX_WIDTH = 400
                        const MAX_HEIGHT = 400

                        var width = img.width
                        var height = img.height

                        if (width > height) {
                            if (width > MAX_WIDTH) {
                                height = height * (MAX_WIDTH / width)
                                width = MAX_WIDTH
                            }
                        } else {
                            if (height > MAX_HEIGHT) {
                                width = width * (MAX_HEIGHT / height)
                                height = MAX_HEIGHT
                            }
                        }

                        const canvas = document.createElement("canvas")
                        canvas.width = width
                        canvas.height = height
                        const ctx = canvas.getContext("2d")
                        ctx.drawImage(img, 0, 0, width, height)
                        const dataurl = canvas.toDataURL(imageFile.type)
                        uploadAddedImage(dataurl)

                    }
                    img.src = e.target.result
                }
                reader.readAsDataURL(imageFile)
            }
        } else {
            toast.warning("Added Maximum Number of Images!")
        }
    }
    const uploadImagesConfig = {
        onUploadProgress: progressEvent => console.log("image upload: ", progressEvent.loaded)
    }

    const uploadAddedImage = async (dataUrl) => {
        try {
            const imageData = new FormData()
            imageData.append("file", dataUrl)
            imageData.append("upload_preset", REACT_APP_PRESET_NAME)
            imageData.append("cloud_name", REACT_APP_CLOUD_NAME)
            imageData.append("folder", "the-foothill-herald")
            const uploadedImage = await uploadImage(imageData, uploadImagesConfig)
            setEditingArticleImages([...editingArticleImages, {
                imgId: uploadedImage.public_id,
                name: uploadedImage.name,
                url: uploadedImage.secure_url
            }])
            loadUpdateEditingArticle()
        } catch (e) {
            toast.error("Failed to Upload Image!")
        }
    }

    const uploadImage = async (imageData, config) => {
        const { data } = await axios.post(`https://api.cloudinary.com/v1_1/${REACT_APP_CLOUD_NAME}/image/upload`, imageData, config)
        return data
    }

    const deleteImage = async (imageId, e) => {
        e.preventDefault()
        cloudinary.v2.uploader.destroy(imageId, (error, result) => {
            toast.success("Deleted Image!")
        }).then((resp) => {
            setEditingArticleImages(editingArticleImages.filter((image) =>
                image.imgId !== imageId
            ))
            loadUpdateEditingArticle()
        }).catch((error) => {
            toast.error("Failed to Delete Image!")
        })
    }

    useEffect(() => {
        let numImages
        if (["top", "middle", "bottom", "left", "right"].includes(editingArticleTemplate)) {
            numImages = 2
        } else if (["top-bottom"].includes(editingArticleTemplate)) {
            numImages = 3
        } else {
            numImages = 1
        }
        setNumImagesToUpload(numImages)

    }, [setNumImagesToUpload, editingArticleTemplate, loadUpdateEditingArticle,
    ])


    //TODO Remember to put keys in safe file

    return (
        <div className='upload-image-background'>
            Images {editingArticleImages.length}/{numImagesToUpload} {editingArticleImages.length > numImagesToUpload &&
                <span className="too-many-images" >Too many images!</span>
            }
            <div className='add-images'>
                <UploadFile onChange={addImage} disabled={disabled}>Add Image</UploadFile>
            </div>
            <div className='added-images'>
                {editingArticleImages.map((image, i) =>
                    <UploadedImage key={i} previewImage={i === 0} id={image.imgId} name={image.name} link={image.url} deleteImage={deleteImage} disabled={disabled} />
                )}
            </div>

        </div>

    )
}

