export const getAddAccount = (state) => {
    return state.userProfile.ownerProfile.addUserAccount.account
}

export const isAddAccountSuccess = (state) => {
    return state.userProfile.ownerProfile.addUserAccount.status === "SUCCESS"
}

export const isAddAccountError = (state) => {
    return state.userProfile.ownerProfile.addUserAccount.status === "ERROR"
}
