import React from 'react'
import Button from '../../components/Button'
import Input from '../../components/Input'
import Modal from '../../components/Modal'
import './index.scss'

export default function AddAccountModal(props) {
    const {
        showAddAccountModal,
        setShowAddAccountModal,
        setAddAccountFirstName,
        setAddAccountLastName,
        setAddAccountEmail,
        addAccountTemporaryPassword,
        addAccountRole,
        setAddAccountRole,
        handleAddAccount
    } =props

    return (
        <Modal height={720} width={500} show={showAddAccountModal} handleShow={() => {setShowAddAccountModal(false)}}>
            <div className='add-account-modal'>
                <div className='add-account-modal title'>
                    Add Account
                </div>
                <div className='add-account-modal inputs'>
                    <div className='modal-inputs'>
                        First Name: <Input width="medium" onChange={setAddAccountFirstName} maxLength={20}>First Name</Input>
                    </div>
                    <div className='modal-inputs'>
                        Last Name: <Input width="medium" onChange={setAddAccountLastName} maxLength={50}>Last Name</Input>
                    </div>
                    <div className='modal-inputs'>
                        User Email: <Input width="medium" onChange={setAddAccountEmail} maxLength={50}>Email Address</Input>
                    </div>
                </div>
                <div className='add-account-modal temporary-password'>
                    <div className='add-account-modal temporary-password password'>
                        Temporary Password:&nbsp;<strong>{addAccountTemporaryPassword}</strong>
                    </div>
                    <div className='add-account-modal temporary-password note'>
                        Remember to copy this Down! The password will generate depending on your form inputs.
                    </div>
                </div>
                <div className='add-account-modal role-selection'>
                    <div className='role-selection-title'>
                        Role
                    </div>
                    <div className='role-selection-buttons'>
                        <div className={`role-selection-button ${addAccountRole === "writer" ? "selected" : "unselected"}`} onClick={() => {setAddAccountRole("writer")}}>
                            Writer
                        </div>
                        <div className={`role-selection-button ${addAccountRole === "editor" ? "selected" : "unselected"}`} onClick={() => {setAddAccountRole("editor")}}>
                            Editor
                        </div>
                        <div className={`role-selection-button ${addAccountRole === "owner" ? "selected" : "unselected"}`} onClick={() => {setAddAccountRole("owner")}}>
                            Owner
                        </div>
                    </div>
                </div>
                <div className='add-account-modal controls'>
                    <Button color="red" onClick={() => {setShowAddAccountModal(false);}}>
                        Cancel
                    </Button>
                    <Button color="green" onClick={() => {handleAddAccount();}}>
                        Add Account
                    </Button>
                </div>
                
            </div>
        </Modal>
  )
}
