import React from 'react'
import './index.scss'
import { historyObject } from '../../../../historyObject';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEye, faPen } from '@fortawesome/free-solid-svg-icons';

export default function GeneralArticle(props) {
    const { id, img, title, authorName, date, category, wordcount, status = "editing" } = props

    return (
        <tr className="general-article-preview">
            <th className='img-title' onClick={() => { historyObject.push(`/article/${id}`) }}>
                <img src={img ? img.url : img} alt="article-preview" className="img" />
                <div className='title'>
                    {title}
                </div>
            </th>
            <th className='date'>
                {date}
            </th>
            <th className='author'>
                {authorName}
            </th>
            <th className='category'>
                {category.toUpperCase()}
            </th>
            <th className='wordcount'>
                {wordcount}
            </th>
            <th className={`status ${status}`}>
                {status.toUpperCase()}
                {
                    status !== "published" &&
                    <div className={"edit-button"} onClick={() => { historyObject.push(`/articleeditingplatform?articleId=${id}`) }}> 
                        {
                            status !== "pending" ?
                                <FontAwesomeIcon icon={faPen} />
                            :
                                <FontAwesomeIcon icon={faEye} />
                        }
                    </div>
                }
            </th>
        </tr>
    )
}
