import { call, put, select } from "redux-saga/effects";
import {
    loadEditingArticleSuccess, loadEditingArticleError,
    loadUpdateEditingArticleSuccess, loadUpdateEditingArticleError,
    loadDeleteEditingArticleSuccess, loadDeleteEditingArticleError, loadPublishEditingArticleSuccess, loadPublishEditingArticleError, loadPublishPendingArticleSuccess, loadPublishPendingArticleError, loadRejectPendingArticleSuccess, loadRejectPendingArticleError, changeIsAuthor
} from ".";
import { historyObject } from "../../../../historyObject";
import * as api from "../../../../services/api";
import { getUserId, getUserRole } from "../../login/user/selector";
import { getCurrEditingArticle, getCurrEditingArticleId } from "./selector";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

// Basic CRUD

export function* loadEditingArticleSaga() {
    try {
        const userId = yield select(getUserId)
        const userRole = yield select(getUserRole)
        const currEditingArticleId = yield select(getCurrEditingArticleId)
        const currEditingArticle = yield call([api, api.loadCurrEditingArticle], userId, userRole, currEditingArticleId)
        const isAuthor = currEditingArticle.authorId === userId
        yield put(loadEditingArticleSuccess(currEditingArticle))
        yield put(changeIsAuthor(isAuthor))
    } catch (e) {
        yield put(loadEditingArticleError(getResponseError(e)))
    }
}

export function* updateEditingArticleSaga() {
    try {
        const userId = yield select(getUserId)
        const currEditingArticleId = yield select(getCurrEditingArticleId)
        const currEditingArticle = yield select(getCurrEditingArticle)

        console.log(currEditingArticle)
        const updatedArticle = yield call([api, api.updatCurrEditingArticle], userId, currEditingArticleId, {
            ...currEditingArticle,
            status: "editing"
        })
        yield put(loadUpdateEditingArticleSuccess(updatedArticle))
    } catch (e) {
        yield put(loadUpdateEditingArticleError(getResponseError(e)))
    }
}

export function* deleteEditingArticleSaga() {
    try {
        const userId = yield select(getUserId)
        const currEditingArticleId = yield select(getCurrEditingArticleId)
        yield call([api, api.deleteCurrEditingArticle], userId, currEditingArticleId)
        yield put(loadDeleteEditingArticleSuccess())
    } catch (e) {
        yield put(loadDeleteEditingArticleError(getResponseError(e)))
    }
}

// Editor Sagas

export function* publishEditingArticleSaga() {
    try {
        const userId = yield select(getUserId)
        const currEditingArticleId = yield select(getCurrEditingArticleId)
        yield call([api, api.publishCurrEditingArticle], userId, currEditingArticleId)
        yield put(loadPublishEditingArticleSuccess())
        historyObject.push(`/userOffice/${userId}`)
    } catch (e) {
        yield put(loadPublishEditingArticleError(getResponseError(e)))
    }
}

export function* publishPendingArticleSaga() {
    try {
        const userId = yield select(getUserId)
        const userRole = yield select(getUserRole)
        const currEditingArticleId = yield select(getCurrEditingArticleId)
        yield call([api, api.publishPendingArticle], userRole, currEditingArticleId)
        yield put(loadPublishPendingArticleSuccess())
        historyObject.push(`/userOffice/${userId}`)
    } catch (e) {
        yield put(loadPublishPendingArticleError(getResponseError(e)))
    }
}

export function* rejectPendingArticleSaga() {
    try {
        const userId = yield select(getUserId)
        const userRole = yield select(getUserRole)
        const currEditingArticleId = yield select(getCurrEditingArticleId)
        yield call([api, api.rejectPendingArticle], userRole, currEditingArticleId)
        yield put(loadRejectPendingArticleSuccess())
        historyObject.push(`/userOffice/${userId}`)
    } catch (e) {
        yield put(loadRejectPendingArticleError(getResponseError(e)))
    }
}