import { call, put } from "redux-saga/effects";
import * as api from "../../../../services/api";
import { loadRefreshTokenSuccess, loadRefreshTokenError } from ".";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* refreshTokenSaga() {
    try {
        const { newToken } = yield call([api, api.refreshToken])
        localStorage.setItem("token", newToken) // saves jwt token to local storage
        yield put(loadRefreshTokenSuccess())
    } catch (e) {
        yield put(loadRefreshTokenError(getResponseError(e)))
        console.log("Error from Refresh Token in Saga: ", e)
    }
}