import React from 'react';
import Dropdown from '../Dropdown';
import "./index.scss";

export default function Pagination(props) {

    const { values=[10, 20, 30], currentPage, changeCurrentPage, itemsPerPage, changeItemsPerPage, totalNumItems, arrows = true } = props;

    const pageNumbers = []

    for (let i = 0; i < Math.ceil(totalNumItems / itemsPerPage); i++) {
        pageNumbers.push(i + 1)
    }

    const paginate = (target) => {
        changeCurrentPage(target)

    }

    const updateItemsPerPage = (val) => {
        changeItemsPerPage(parseInt(val))
        paginate(1)// To reset page number to 1
    }
    return <div className="pagination">
        {arrows && <div className="pagination-buttons">
            &lt;
            &nbsp;
            <div style={{ cursor: "pointer" }} onClick={() => { paginate(currentPage > 1 ? currentPage - 1 : currentPage) }}>
                Previous
            </div>
            &nbsp;&nbsp;&nbsp;
            {pageNumbers.map((pageNumber => (
                <div key={pageNumber} className={"pagination-page-numbers" + (pageNumber === currentPage ? " selected" : "")} onClick={() => { paginate(pageNumber) }}>
                    {pageNumber}
                </div>
            )))}
            &nbsp;&nbsp;&nbsp;
            <div style={{ cursor: "pointer" }} onClick={() => { paginate(currentPage < pageNumbers.length ? currentPage + 1 : currentPage) }}>
                Next
            </div>
            &nbsp;
            &gt;
        </div>}
        <div className="pagination-dropdown">
            Articles per Page: &nbsp;
            <Dropdown values={values} setValue={updateItemsPerPage}/>
        </div>



    </div>;
}
