import React, { useEffect } from 'react'
import { Switch, Route } from 'react-router'

import AboutUs from '../pages/AboutUs'
import ArticleEditingPlaform from '../pages/ArticleEditingPlatform'
import ContactUs from '../pages/ContactUs'
import Home from '../pages/Home'
import Login from '../pages/Login'
import UserProfile from '../pages/UserProfile'
import { LoggedInGuard } from './guard'
import ViewArticle from '../pages/ViewArticle'
import UserOffice from '../pages/UserOffice'
import Authentication from '../pages/Authentication'
import { ToastContainer } from 'react-toastify'
import { loadRefreshToken } from '../../store/reducer/login/refreshToken'

function Router() {

    useEffect(() => {
        const token  = localStorage.getItem("token")
        if (!!token) {
            loadRefreshToken()
            console.log("refreshed jwt")
        }
    }, [])

    return (
        <>
            <Switch>
                <Route exact path="/" component={Home} />
                <Route path="/aboutus" component={AboutUs} />
                <Route path="/contactus" component={ContactUs} />
                <Route path="/login" component={Login} />
                <Route path="/authentication/:userId" component={LoggedInGuard(Authentication)} />
                <Route path="/userprofile/:userId" component={LoggedInGuard(UserProfile)} />
                <Route path="/useroffice/:userId" component={LoggedInGuard(UserOffice)} />
                <Route path="/articleeditingplatform" component={ArticleEditingPlaform} />
                <Route path="/article/:id" component={ViewArticle} />
            </Switch>
            <ToastContainer position="bottom-right" autoClose={5000} />
        </>
    )
}

export default Router
