import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import Headerfooter from '../../layout/headerfooter'
import "./index.scss"
import { loadAuthenticationPassword } from "../../../store/reducer/login/authentication/index"
import { changeUser } from "../../../store/reducer/login/user/index"
import { isAuthenticationSuccess, isAuthenticationError, getAuthentication } from "../../../store/reducer/login/authentication/selector"
import Input from '../../components/Input'
import Button from '../../components/Button'
import { toast } from 'react-toastify'

const mapStateToProps = (state) => ({
    authenticationInputs: getAuthentication(state),
    isAuthenticationSuccess: isAuthenticationSuccess(state),
    isAuthenticationError: isAuthenticationError(state),
})

const mapDispatchToProps = { loadAuthenticationPassword, changeUser }

function Authentication(props) {
    const { authenticationInputs, isAuthenticationSuccess, isAuthenticationError, loadAuthenticationPassword, changeUser } = props

    const [newPassword, setNewPassword] = useState(authenticationInputs.newPassword)
    const [confirmNewPassword, setConfirmNewPassword] = useState(authenticationInputs.confirmNewPassword)

    const handleChangePassword = () => {
        loadAuthenticationPassword({
            newPassword: newPassword,
            confirmNewPassword: confirmNewPassword,
        })
    }

    useEffect(() => {
        if (isAuthenticationSuccess) {
            toast.success("Password Changed Successfully! Your Account is now Verified!")
        }
        if (isAuthenticationError) {
            toast.error("Password Changed Failed!")
        }

    }, [isAuthenticationSuccess, isAuthenticationError, changeUser])

    return (
        <Headerfooter title={"middle"}>
            <div className='authentication-background'>
                <div className='authentication-title'>
                    This is your first time logging on.
                    Please Change Your Password
                    to Verify Your Account!
                </div>
                <div className='authentication-inputs'>
                    <h3>New Password</h3>
                    <Input type={"password"} defaultValue={newPassword} onChange={setNewPassword} width={"long"}>
                        New Password
                    </Input>
                    <h3>Confirm New Password</h3>
                    <Input type={"password"} defaultValue={confirmNewPassword} onChange={setConfirmNewPassword} width={"long"} background={"white"} rounded={true}>
                        Confirm New Password
                    </Input>
                    <Button onClick={handleChangePassword} type={"login"} size={"long"} background={"white"} rounded={true}>
                        Change Password
                    </Button>
                </div>
            </div>
        </Headerfooter>
    )
}

export default connect(mapStateToProps, mapDispatchToProps)(Authentication)
