import React from 'react'
import Button from '../../components/Button'
import Input from '../../components/Input'
import Modal from '../../components/Modal'
import "./index.scss"

export default function ChangePasswordModal(props) {

    const {
        showChangePasswordModal,
        setShowChangePasswordModal,
        changePasswordOld,
        setChangePasswordOld,
        changePasswordNew,
        setChangePasswordNew,
        changePasswordConfirmNew,
        setChangePasswordConfirmNew,
        handleChangePassword,
        
    } = props


  return (
    <Modal height={420} width={300} show={showChangePasswordModal} handleShow={() => { setShowChangePasswordModal(false) }}>
        <div className='change-password-modal'>
            <div className='title'>
                Change Password
            </div>
            <div className='old-password'>
                Old Password:
                <Input width="medium" defaultValue={changePasswordOld} onChange={setChangePasswordOld}>Input Old Password</Input>
            </div>
            <div className='new-password'>
                New Password: <Input width="medium" type="password" defaultValue={changePasswordNew} onChange={setChangePasswordNew}>Input New Password</Input>
            </div>
            <div className='confirm-new-password'>
                Confirm New Password: <Input width="medium" type="password" defaultValue={changePasswordConfirmNew} onChange={setChangePasswordConfirmNew}>Input New Password</Input>
            </div>
            <div className='change-password'>
                <Button onClick={() => { handleChangePassword() }}>Change Password</Button>
            </div>
        </div>
    </Modal>
  )
}
