export const getUser = (state) => {
    return state.auth.user.user
}

export const getUserId = (state) => {
    return !!state.auth.user.user ? state.auth.user.user._id : null
}

export const getUserName = (state) => {
    return !!state.auth.user.user ? {
        firstName: state.auth.user.user.firstName, 
        lastName: state.auth.user.user.lastName
    } : null
}

export const getUserRole = (state) => {
    return !!state.auth.user.user ? state.auth.user.user.role : null
}

export const getUserEmail = (state) => {
    return !!state.auth.user.user ? state.auth.user.user.email : null
}

export const getUserBio = (state) => {
    return !!state.auth.user.user ? state.auth.user.user.bio : null
}

export const getCredentials = (state) => {
    return state.auth.user.credentials
}

export const isLoggedIn = (state) => {
    return !!state.auth.user.user // !! returns true or falsy nature of variable
}

export const isDisabled = (state) => {
    return !!state.auth.user.user && state.auth.user.user.disabled
}

export const isAuthenticated = (state) => {
    return !!state.auth.user.user && state.auth.user.user.authenticated
}

export const isUserOwner = (state) => {
    return !!state.auth.user.user && state.auth.user.user.role === "owner"
}

export const isUserWriter = (state) => {
    return !!state.auth.user.user && state.auth.user.user.role === "writer"
}

export const isLoginSuccess = (state) => {
    return state.auth.user.status === "SUCCESS"
}

export const isLoginError = (state) => {
    return state.auth.user.status === "ERROR"
}

