import { call, put, select } from "redux-saga/effects";
import * as api from "../../../services/api";
import { loadError } from ".";
import { getCurrViewingArticleId } from "./selector";
import { changeCurrViewingArticle } from "./index"

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* viewArticleSaga() { // envoked when soemthing else
    try {
        const currViewingArticleId = yield select(getCurrViewingArticleId)// takes selector as paramater
        const currViewingArticle = yield call([api, api.fetchCurrViewingArticle], currViewingArticleId) // paramaters received in get login data
        yield put(changeCurrViewingArticle(currViewingArticle))
    } catch (e) {
        yield put(loadError(getResponseError(e)))
        console.log("Error from View Article Saga: ", e)
    }

}