import React, { useMemo } from 'react'
import { connect } from 'react-redux'
import { Redirect, useLocation } from 'react-router'
import { getUser, isLoggedIn } from "../../../store/reducer/login/user/selector"
import { isAuthenticated } from "../../../store/reducer/login/user/selector"
// import { getUser, isLoggedIn } from '../../../store/reducer/login/selector'



function Guard(Component) {
    const mapStateToProps = (state) => ({
        user: getUser(state),
        loggedIn: isLoggedIn(state),
        isAuthenticated: isAuthenticated(state),
    })

    const GuardComponent = (props) => {
        const { user, loggedIn, isAuthenticated } = props

        const path = useLocation().pathname.split("/")[1]

        const redirectPath = useMemo(() => {
            if (loggedIn && !isAuthenticated) {
                return `/authentication/${user.id}`
            }
            return "/"
        }, [user, loggedIn, isAuthenticated])

        // console.log({
        //     user,
        //     loggedIn,
        //     isAuthenticated,
        //     redirectPath
        // })

        const redirect = () => {
            if ((path === "authentication" && loggedIn) || (loggedIn && isAuthenticated)) {
                return <Component {...props} />
            }
            return <Redirect push={true} to={redirectPath} />
        }

        return (<>
            {
                redirect()
            }
        </>)
    }

    return connect(mapStateToProps, null)(GuardComponent)
}

export const LoggedInGuard = (Component) => Guard(Component)
