import React, { useEffect } from 'react'
import './index.scss'
import LoggedInLayout from '../../layout'
import HeaderFooter from '../../layout/headerfooter'
import StandardTemplate from './HomeTemplates/StandardTemplate'
import CategorizedTemplate from './HomeTemplates/CategorizedTemplate'
import Pagination from '../../components/Pagination'
import { loadHomepagePreviewArticles, changeCurrentHomePage, changePreviewArticlesPerPage, changePreviewArticlesFilter } from '../../../store/reducer/previewArticles'
import { getHomepageCurrentPage, getHomepageCurrentPreviewArticlesPerPage, getHomepageCurrentPreviewArticlesList, getHomepageCurrentTotalPreviewArticlesNum, getHomepageCurrentPreviewArticlesFilter } from '../../../store/reducer/previewArticles/selector'
import { connect } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHome } from '@fortawesome/free-solid-svg-icons'
import Button from '../../components/Button'

const mapStateToProps = (state) => ({
    currentHomePage: getHomepageCurrentPage(state),
    previewArticlesPerPage: getHomepageCurrentPreviewArticlesPerPage(state),
    currentPreviewArticles: getHomepageCurrentPreviewArticlesList(state),
    totalNumPreviewArticles: getHomepageCurrentTotalPreviewArticlesNum(state),
    filter: getHomepageCurrentPreviewArticlesFilter(state),
})

const mapDispatchToProps = { loadHomepagePreviewArticles, changeCurrentHomePage, changePreviewArticlesPerPage, changePreviewArticlesFilter }

function Home(props) {

    const { currentHomePage, previewArticlesPerPage, currentPreviewArticles, totalNumPreviewArticles, filter, loadHomepagePreviewArticles, changeCurrentHomePage, changePreviewArticlesPerPage, changePreviewArticlesFilter } = props

    const navBarList = {
        "News": [],
        "Opinion": [],
        "Sports": [],
        "Entertainment": [],
        "Clubs": [],
        "Advertising": [],
    }

    useEffect(() => {
        loadHomepagePreviewArticles()
    }, [filter, currentHomePage, previewArticlesPerPage, loadHomepagePreviewArticles])

    console.log(filter)

    return (
        <HeaderFooter title="normal">
            <div className="home">
                <LoggedInLayout>
                    <div className='home-nav-bar'>
                        <div className='home-nav-bar-links' onClick={() => { changePreviewArticlesFilter("") }}>
                            <FontAwesomeIcon icon={faHome} className="home-nav-bar-home" />
                        </div>
                        {
                            Object.keys(navBarList).map((curr) =>
                                <div key={curr} className='home-nav-bar-links' onClick={() => { changePreviewArticlesFilter(curr.toLowerCase()) }}>
                                    <Button type="navbar">
                                        {curr}
                                    </Button>
                                </div>)

                        }
                    </div>
                    {
                        currentPreviewArticles.length > 0 ?
                        filter === "" ?
                            <StandardTemplate
                                currentPreviewArticles={currentPreviewArticles}
                            />
                            :
                            <CategorizedTemplate
                                currentPreviewArticles={currentPreviewArticles}
                                filter={filter}
                            />

                        : <div className="loading-article-previews">Loading Article Previews...</div>
                    }
                    <Pagination
                        currentPage={currentHomePage}
                        changeCurrentPage={changeCurrentHomePage}
                        itemsPerPage={previewArticlesPerPage}
                        changeItemsPerPage={changePreviewArticlesPerPage}
                        totalNumItems={totalNumPreviewArticles}
                    />
                </LoggedInLayout>
            </div>
        </HeaderFooter>

    )


}

export default connect(mapStateToProps, mapDispatchToProps)(Home)



