import React, { useEffect, useRef, useState } from 'react'
import { connect } from 'react-redux'
import { getUserId, getUserName, isLoggedIn } from '../../../store/reducer/login/user/selector'
import { changeUser } from '../../../store/reducer/login/user/index'
import "./index.scss"
import { historyObject } from '../../../historyObject'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircleUser } from '@fortawesome/free-solid-svg-icons'
import Button from '../Button'

const mapStateToProps = (state) => ({
    isLoggedIn: isLoggedIn(state),
    userName: getUserName(state),
    userId: getUserId(state),
})

const mapDispatchToProps = { changeUser }

function ProfileDropdown(props) {
    const { isLoggedIn, userName, userId, changeUser } = props

    const [isDropDownVisible, setIsDropDownVisible] = useState(false)
    const dropdownRef = useRef(null)
    const [name, setName] = useState("")

    // 
    const goToUserProfile = () => [
        historyObject.push(`/userprofile/${userId}`)
    ]

    const goToUserOffice = () => [
        historyObject.push(`/useroffice/${userId}`)
    ]

    const logout = () => {
        changeUser(null)
        historyObject.push("/")
    }
    //
    const handleHideDropdown = (event) => {
        if (event.key === "Escape") {
            setIsDropDownVisible(false)
        }
    }

    const handleClickOutside = (event) => {
        if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
            setIsDropDownVisible(false)
        }
    }

    useEffect(() => {
        document.addEventListener("keydown", handleHideDropdown, true)
        document.addEventListener("click", handleClickOutside, true)
    }, [isDropDownVisible])

    useEffect(() => {
        if (isLoggedIn) {
            setName(userName.firstName.slice(0, 1).toUpperCase() + userName.firstName.slice(1) + " " + userName.lastName.slice(0, 1).toUpperCase() + ".")
        }
    }, [isLoggedIn, setName, userName])


    return (
        <div className='profile-dropdown' ref={dropdownRef}>
            {
                isLoggedIn && name !== "" && <div className="logged-in-user" onClick={() => {
                    setIsDropDownVisible(!isDropDownVisible)
                }}>
                    {name} &nbsp;&nbsp;&nbsp; <FontAwesomeIcon icon={faCircleUser} />
                </div> // instead of name could be profile picture
            }
            { // openUserDropdown
                isLoggedIn && isDropDownVisible && <div id="logged-in-user-dropdown" className='logged-in-user-dropdown' >
                    <Button type="option" onClick={goToUserProfile}>Profile</Button>
                    <Button type="option" onClick={goToUserOffice}>Dashboard</Button>
                    <Button type="option" onClick={logout}>Logout</Button>
                    {/* <Button type="option" onClick={() => {;historyObject.push("/")}}>Sign Out</Button> */}
                </div>
            }
        </div>
    )
}

export default connect(mapStateToProps, mapDispatchToProps)(ProfileDropdown)
