import React from 'react'

export default function CheckBoxes(props) {
    const {values=[], name="banana", setValueList, currValueList, disabled=false} = props

    const handleCheck = (value) => {
        if (currValueList.includes(value)){
            const newCurrValueList = currValueList.filter((e) => (
                e !== value
            ))
            setValueList(newCurrValueList)
        } else{
            const newCurrValueList = [value, ...currValueList]
            setValueList(newCurrValueList)
        }

        
    }

    return (
        <>
            {values.map((value) => {
                return (
                    <span key={value}>
                        <input type="checkbox" name={name} value={value} disabled={disabled} checked={currValueList.includes(value)} onChange={() => {handleCheck(value)}}/>
                        {value}
                    </span>
                )
            })}
        </>
    )
}
