import React from 'react'
import './index.scss'

export default function UploadedImage(props) {
    const { id, previewImage, name, link, deleteImage, disabled = true } = props

    const handleDeleteImage = (e) => {
        deleteImage(id, e)
    }

    return (
        <div className={`uploaded-image ${previewImage ? "preview" : "article"}`}>
            {!disabled &&
                <button className='delete-button' onClick={handleDeleteImage}>
                    x
                </button>
            }
            <img src={link} alt={name} />
        </div>
    )
}
