import React, { useEffect, useState } from 'react'
import ArticleEditor from './ArticleEditor'
import HeaderFooter from '../../layout/headerfooter'
import "./index.scss"
import ArticleEditorSettings from './ArticleEditorSettings'
import { connect } from 'react-redux'
import {
    loadEditingArticle, loadEditingArticleIdle, changeCurrEditingArticle,
    loadUpdateEditingArticle, loadUpdateEditingArticleIdle,
    loadDeleteEditingArticle, loadDeleteEditingArticleIdle,
    loadPublishEditingArticle,
    loadPublishPendingArticle,
    loadRejectPendingArticle,
} from '../../../store/reducer/articleEditingPlatform/articleEditor';
import { useLocation } from 'react-router'
import { getStatus, getCurrServerArticle, isServerArticleError, isDeleteEditingArticleSuccess, isDeleteEditingArticleError, isUpdateEditingArticleSuccess, isUpdateEditingArticleError, isDeleteEditingArticleIdle, getIsAuthor } from '../../../store/reducer/articleEditingPlatform/articleEditor/selector'
import ProfileDropdown from '../../components/ProfileDropdown'
import { toast } from 'react-toastify'
import { historyObject } from '../../../historyObject'
import { getUserId, getUserRole } from '../../../store/reducer/login/user/selector'

const mapStateToProps = (state) => ({
    userId: getUserId(state),
    userRole: getUserRole(state),
    isAuthor: getIsAuthor(state),
    status: getStatus(state),
    serverArticle: getCurrServerArticle(state),
    isServerArticleError: isServerArticleError(state),
    isUpdateEditingArticleSuccess: isUpdateEditingArticleSuccess(state),
    isUpdateEditingArticleError: isUpdateEditingArticleError(state),
    isDeleteEditingArticleSuccess: isDeleteEditingArticleSuccess(state),
    isDeleteEditingArticleError: isDeleteEditingArticleError(state),
    isDeleteEditingArticleIdle: isDeleteEditingArticleIdle(state),
    // currEditingArticle : getCurrEditingArticle(state)
})

const mapDispatchToProps = {
    changeCurrEditingArticle,
    loadEditingArticle, loadEditingArticleIdle,
    loadUpdateEditingArticle, loadUpdateEditingArticleIdle,
    loadDeleteEditingArticle, loadDeleteEditingArticleIdle,

    loadPublishEditingArticle,
    loadPublishPendingArticle,
    loadRejectPendingArticle,
}

function ArticleEditingPlaform(props) {

    const {
        userId, userRole, isAuthor,
        status,
        serverArticle, isServerArticleError,
        changeCurrEditingArticle,
        loadEditingArticle, loadEditingArticleIdle,
        isUpdateEditingArticleSuccess, isUpdateEditingArticleError, loadUpdateEditingArticle, loadUpdateEditingArticleIdle,
        isDeleteEditingArticleSuccess, isDeleteEditingArticleError, isDeleteEditingArticleIdle, loadDeleteEditingArticle, loadDeleteEditingArticleIdle,
        loadPublishEditingArticle,
        loadPublishPendingArticle,
        loadRejectPendingArticle,
    } = props

    const [saved, setSaved] = useState(false)

    const [editingArticleTitle, setEditingArticleTitle] = useState("")
    const [editingArticleCategory, setEditingArticleCategory] = useState("")
    const [editingArticleTemplate, setEditingArticleTemplate] = useState("")
    const [editingArticleContent, setEditingArticleContent] = useState("")
    const [editingArticleWordCount, setEditingArticleWordCount] = useState("")
    const [editingArticleImages, setEditingArticleImages] = useState([])

    const [editingArticleSavedDate, setEditingArticleSavedDate] = useState("")
    

    const params = useLocation().search.slice(1).split("&")
    const articleId = params[0].split("=")[1]

    const getNumImages = () => {
        let numImages
        if (["top", "middle", "bottom", "left", "right"].includes(editingArticleTemplate)) {
            numImages = 2
        } else if (["top-bottom"].includes(editingArticleTemplate)) {
            numImages = 3
        } else {
            numImages = 1
        }
        return numImages
    }

    const handleSaveEditingArticle = () => {
        const numImages = getNumImages()
        if (editingArticleImages.length > numImages) {
            toast.warning("Uploaded Too Many Images!")
        } else if (editingArticleWordCount > 1500) {
            toast.warning("Exceeded Maximum Wordcount! (1500)")
        } else{
            loadUpdateEditingArticle()
        }
    }

    const handlePublishEditingArticle = () => {
        const numImages = getNumImages()
        if (editingArticleImages.length !== numImages) {
            toast.warning("Please Upload the Correct Number of Images!")
        } else if (editingArticleWordCount < 20) {
            toast.warning("Please Write More!")
        } else if (editingArticleWordCount > 1500) {
            toast.warning("Exceeded Maximum Wordcount! (1500)")
        } else{
            loadPublishEditingArticle()
        }
    }

    //TODO figur out how to display if article is saved or automatic save
    const handlePreviewEditingArticle = () => {
        const numImages = getNumImages()
        if (editingArticleImages.length !== numImages) {
            toast.warning("Please Upload the Correct Number of Images!")
        } else {
            historyObject.push(`/article/${articleId}`)
        }
    }

    useEffect(() => {
        if (isDeleteEditingArticleIdle) {
            loadEditingArticle(articleId)
            console.log("loading editing article")
        }
    }, [
        articleId,
        loadEditingArticle,
        isDeleteEditingArticleIdle,
    ])

    useEffect(() => {
        setEditingArticleTitle(serverArticle.title)
        setEditingArticleCategory(serverArticle.category)
        setEditingArticleTemplate(serverArticle.template)
        setEditingArticleContent(serverArticle.content)
        setEditingArticleWordCount(serverArticle.wordcount)
        setEditingArticleImages(serverArticle.imgs)
        setEditingArticleSavedDate(serverArticle.date)
    }, [serverArticle,
        setEditingArticleTitle, setEditingArticleCategory, setEditingArticleTemplate, setEditingArticleContent, setEditingArticleWordCount, setEditingArticleImages,
    ])

    console.log("editingArticleSavedDate: ", editingArticleSavedDate)

    useEffect(() => {
        const newCurrEditingArticle = {
            title: editingArticleTitle,
            template: editingArticleTemplate, // none, top, bottom, left, right, middle, top-bottom
            date: (new Date()).toString(),
            content: editingArticleContent, // stateToHTML
            category: editingArticleCategory.toLowerCase(),
            wordcount: editingArticleWordCount,
            imgs: editingArticleImages,
        }
        changeCurrEditingArticle(newCurrEditingArticle)
        // console.log("changed editing article: ", newCurrEditingArticle)
        // console.log((new Date()).getTime() / 1000 - (new Date(editingArticleSavedDate)).getTime() / 1000)
        // console.log((new Date()).getTime() / 1000)
        // console.log((new Date(editingArticleSavedDate)).getTime() / 1000)
        // console.log(new Date((new Date(editingArticleSavedDate)).getTime()))
        // if ((new Date()).getTime() / 1000 - (new Date(editingArticleSavedDate)).getTime() / 1000 > 1){
        //     setSaved(false)

        // }
        // console.log((new Date()).getTime() / 1000 - (new Date(editingArticleSavedDate)).getTime() / 1000)


        // if (isUpdateEditingArticleSuccess){
        //     loadUpdateEditingArticleIdle()
        // } else{
        //     setSaved(false)
        // }
        
    }, [setSaved, editingArticleSavedDate, 
        changeCurrEditingArticle, editingArticleTitle, editingArticleCategory, editingArticleTemplate, editingArticleContent, editingArticleWordCount, editingArticleImages])

    // Toasts

    //Load Article Toast
    useEffect(() => {
        if (isServerArticleError) {
            historyObject.push(`/useroffice/${userId}`)
            toast.error("Failed to Load Article! ")
            loadEditingArticleIdle()
        }
    }, [isServerArticleError, userId, loadEditingArticleIdle])

    //Update Article Toasts
    useEffect(() => {
        if (isUpdateEditingArticleSuccess) {
            // toast.success("Saved Article!")
            setSaved(true)
            // loadUpdateEditingArticleIdle()
        }
    }, [isUpdateEditingArticleSuccess, loadUpdateEditingArticleIdle])

    useEffect(() => {
        if (isUpdateEditingArticleError) {
            toast.error("Failed to Save Article!")
            loadUpdateEditingArticleIdle()
        }
    }, [isUpdateEditingArticleError, loadUpdateEditingArticleIdle])


    // Delete Article Toasts
    useEffect(() => {
        if (isDeleteEditingArticleSuccess) {
            historyObject.push(`/useroffice/${userId}`)
            toast.success("Successfully Deleted Article!")
            loadDeleteEditingArticleIdle()
        }
    }, [userId, isDeleteEditingArticleSuccess, loadDeleteEditingArticleIdle])

    useEffect(() => {
        if (isDeleteEditingArticleError) {
            toast.error("Failed to Delete Article!")
            loadDeleteEditingArticleIdle()
        }
    }, [isDeleteEditingArticleError, loadDeleteEditingArticleIdle])



    return (
        <HeaderFooter title='none'>
            <div className='article-editing-platform-background'>
                <div className='article-editing-platform'>
                    <ArticleEditor
                        status={status}
                        userRole={userRole}
                        isAuthor={isAuthor}

                        setEditingArticleTitle={setEditingArticleTitle}
                        setEditingArticleContent={setEditingArticleContent}
                        setEditingArticleWordCount={setEditingArticleWordCount}

                        editingArticleTitle={editingArticleTitle}
                        serverArticle={{ title: serverArticle.title, content: serverArticle.content }} // JUST REPRESENT STARTING VALUE, only contains title and content

                        loadUpdateEditingArticle={handleSaveEditingArticle}
                        loadDeleteEditingArticle={loadDeleteEditingArticle}
                        loadPublishEditingArticle={handlePublishEditingArticle} // Special Case, need checks 
                        loadPublishPendingArticle={loadPublishPendingArticle}
                        loadRejectPendingArticle={loadRejectPendingArticle}
                        handlePreviewEditingArticle={handlePreviewEditingArticle}
                    />
                    <ArticleEditorSettings
                        editingArticleSavedDate={editingArticleSavedDate}

                        status={status}
                        isAuthor={isAuthor}
                        setEditingArticleCategory={setEditingArticleCategory}
                        setEditingArticleTemplate={setEditingArticleTemplate}
                        setEditingArticleImages={setEditingArticleImages}
                        setEditingArticleWordCount={setEditingArticleWordCount}

                        saved={saved}
                        serverArticle={{ category: serverArticle.category, template: serverArticle.template, wordcount: serverArticle.wordcount }} // JUST REPRESENT STARTING VALUE

                        editingArticleCategory={editingArticleCategory}
                        editingArticleTemplate={editingArticleTemplate}
                        editingArticleImages={editingArticleImages}
                        editingArticleWordCount={editingArticleWordCount}

                        loadUpdateEditingArticle={loadUpdateEditingArticle}
                    />
                </div>
                <div className='article-editing-platform-profile-dropdown-background'>
                    <div className='article-editing-platform-profile-dropdown'>
                        <ProfileDropdown />
                    </div>
                </div>
            </div>

        </HeaderFooter>
    )

}

export default connect(mapStateToProps, mapDispatchToProps)(ArticleEditingPlaform)
