import { createSlice } from "@reduxjs/toolkit";

//TODO
const initialState = {
    status: "idle",
    error: "",
    imgs: null,
}

const slice = createSlice({
    initialState,
    name: "imagesEditor",
    reducers: {
        loadImagesEditor: (state, action) => {
            state.imgs = action.payload
            state.status = "LOADING"
        },
        loadImagesEditorSuccess: (state) => {
            state.status = "SUCCESS"
        },
        loadImagesEditorError: (state) => {
            state.status = "ERROR"
        },
    },
})


const { reducer, actions } = slice
export const {
    loadImagesEditor, loadImagesEditorSuccess, loadImagesEditorError
} = actions
export default reducer