import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    status : "idle",
    error : "",
    account : {
        firstName : "",
        lastName : "",
        email : "",
        role : "",
        bio: "",
        disabled: false,
        authenticated: false,
        temporaryPassword : "",
    },
}

const slice = createSlice({
    initialState,
    name : "addUserAccount",
    reducers : {
        changeAddAccount : (state, action) => {
            state.account.firstName = action.payload.firstName
            state.account.lastName = action.payload.lastName
            state.account.email = action.payload.email
            state.account.role = action.payload.role
            state.account.bio = action.payload.bio
            state.account.disabled = action.payload.disabled
            state.account.authenticated = action.payload.authenticated
            state.account.temporaryPassword = action.payload.temporaryPassword
        },
        loadAddAccount : (state) => {
            state.status = "LOADING"
        },
        loadAddAccountSuccess : (state) => {
            state.status = "SUCCESS"
        },
        loadAddAccountError : (state) => {
            state.status = "ERROR"
        },
    },
})


const {reducer, actions} = slice
export const {
    changeAddAccount, loadAddAccount, loadAddAccountSuccess, loadAddAccountError
} = actions
export default reducer