import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../../services/api";
import { loadUpdateUserProfileError, loadUpdateUserProfileSuccess } from ".";
import { getUserUpdateProfile } from "./selector";
import { getUserId } from "../../../login/user/selector";
import { changeUser } from "../../../login/user";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* userProfileUpdateUserProfileSaga() {
    try {
        const _id = yield select(getUserId)
        const changes = yield select(getUserUpdateProfile)
        const newUser = yield call([api, api.updateUserProfile], _id, changes)
        yield put(changeUser(newUser))
        yield put(loadUpdateUserProfileSuccess())
    } catch (e) {
        yield put(loadUpdateUserProfileError(getResponseError(e)))
        console.log("Error from User Profile Change Profile Saga: ", e)
    }
}