import { call, put } from "redux-saga/effects";
import * as api from "../../../../../services/api";
import { loadUserAccountError, loadUserAccountsSuccess } from ".";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* ownerProfileUserAccountsSaga() {
    try {
        const data = yield call([api, api.getUserAccounts])
        //TODO Console log data
        yield put(loadUserAccountsSuccess(data))
    } catch (e) {
        yield put(loadUserAccountError(getResponseError(e)))
        console.log("Error from Owner Profile Saga (Accounts): ", e)
    }
}

