import React, { useState } from 'react'
import { connect } from 'react-redux'
import './index.scss'
import { faCog, faEye, faEyeSlash, faTriangleExclamation } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import AccountSettingsModal from '../../../modals/AccountSettingsModal'
import DeleteAccountModal from '../../../modals/DeleteAccountModal'
import { changeCurrAccountSettings, loadUpdateAccount, loadDeleteAccount } from '../../../../store/reducer/userProfile/ownerProfile/currUserAccountSettings'
import { loadUserAccounts } from '../../../../store/reducer/userProfile/ownerProfile/userAccounts'
import { getUserId } from '../../../../store/reducer/login/user/selector'
import { toast } from 'react-toastify'

const matchStateToProps = (state) => ({
    currUserId: getUserId(state),
})

const matchDispatchToProps = {
    changeCurrAccountSettings,
    loadUpdateAccount,
    loadDeleteAccount,
    loadUserAccounts,

}

function AccountDetails(props) {

    const { currUserId, account, role, color, changeCurrAccountSettings, loadUpdateAccount, loadDeleteAccount, loadUserAccounts } = props

    const id = account._id
    const firstName = account.firstName.slice(0, 1).toUpperCase() + account.firstName.slice(1)
    const lastName = account.lastName.slice(0, 1).toUpperCase() + account.lastName.slice(1)
    const email = account.email
    const disabled = account.disabled
    const authenticated = account.authenticated
    const temporaryPassword = account.temporaryPassword
    
    // Account Settings
    const [showAccountSettingsModal, setShowAccountSettingsModal] = useState(false)

    const [accountSettingsFirstName, setAccountSettingsFirstName] = useState(firstName)
    const [accountSettingsLastName, setAccountSettingsLastName] = useState(lastName)
    const [accountSettingsEmail, setAccountSettingsEmail] = useState(email)
    const [accountSettingsRole, setAccountSettingsRole] = useState(role)

    const handleChangeAccountSettings = () => {
        changeCurrAccountSettings({
            id: id,
            firstName: accountSettingsFirstName,
            lastName: accountSettingsLastName,
            email: accountSettingsEmail,
            role: accountSettingsRole,
            disabled: disabled,
            authenticated: authenticated,
        })
        loadUpdateAccount()
        loadUserAccounts()
    }

    const handleOpenAccountSettingsModal = () => {
        if (currUserId !== account._id){
            setShowAccountSettingsModal(true) 
        } else{
            toast.warning("Please Edit an Account Other than Yours!")
        }
    }

    // Account Deletion
    const [showDeleteAccountModal, setShowDeleteAccountModal] = useState(false)

    const [accountDeletionConfirmation, setAccountDeletionConfirmation] = useState("")
    const handleDeleteAccountSettings = () => {
        changeCurrAccountSettings({
            id: id,
            firstName: accountSettingsFirstName,
            lastName: accountSettingsLastName,
            email: accountSettingsEmail,
            role: accountSettingsRole,
            disabled: disabled,
            authenticated: authenticated,
        })
        loadDeleteAccount()
        loadUserAccounts()
    }

    // Account Disable

    const handleDisableAccountSettings = () => {
        changeCurrAccountSettings({
            id: id,
            firstName: accountSettingsFirstName,
            lastName: accountSettingsLastName,
            email: accountSettingsEmail,
            role: accountSettingsRole,
            disabled: !disabled,
            authenticated: authenticated,
        })
        loadUpdateAccount()
        loadUserAccounts()
    }

    return (
        <>
            <div className={`account-details-background ${color}`}>
                <div className='account-details-left'>
                    <div className='account-details-name'>
                        {firstName.toUpperCase().slice(0, 1) + firstName.slice(1)}
                    </div>
                    <div className='account-details-email'>
                        {email}
                    </div>
                </div>
                <div className='account-details-right' >
                    {
                        authenticated ?
                            <></>
                            :
                            <FontAwesomeIcon icon={faTriangleExclamation} />
                    }
                    {
                        role !== "owner" ?
                            <FontAwesomeIcon icon={disabled ? faEyeSlash : faEye} className="account-details-setting" onClick={() => { handleDisableAccountSettings() }} />
                            :
                            <></>
                    }
                    <FontAwesomeIcon icon={faCog} className="account-details-setting" onClick={handleOpenAccountSettingsModal} />
                </div>
            </div>

            <AccountSettingsModal
                showAccountSettingsModal={showAccountSettingsModal}
                setShowAccountSettingsModal={setShowAccountSettingsModal}
                role={role}
                firstName={firstName}
                setAccountSettingsFirstName={setAccountSettingsFirstName}
                lastName={lastName}
                setAccountSettingsLastName={setAccountSettingsLastName}
                email={email}
                setAccountSettingsEmail={setAccountSettingsEmail}

                authenticated={authenticated}
                temporaryPassword={temporaryPassword}

                accountSettingsRole={accountSettingsRole}
                setAccountSettingsRole={setAccountSettingsRole}
                setShowDeleteAccountModal={setShowDeleteAccountModal}
                handleChangeAccountSettings={handleChangeAccountSettings}
            />

            <DeleteAccountModal
                showDeleteAccountModal={showDeleteAccountModal}
                setShowDeleteAccountModal={setShowDeleteAccountModal}
                firstName={firstName}
                lastName={lastName}
                setAccountDeletionConfirmation={setAccountDeletionConfirmation}
                handleDeleteAccountSettings={handleDeleteAccountSettings}
                setShowAccountSettingsModal={setShowAccountSettingsModal}
                accountDeletionConfirmation={accountDeletionConfirmation}
            />
        </>
    )
}

export default connect(matchStateToProps, matchDispatchToProps)(AccountDetails)

