import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    status: "idle",
    error: "",
    role: "",
    inputs: {
        firstName: "",
        lastName: "",
        email: "",
        bio: "",
    },
}

const slice = createSlice({
    initialState,
    name: "updateUserProfile",
    reducers: {
        loadUpdateUserProfile: (state, action) => {
            state.inputs.firstName = action.payload.firstName
            state.inputs.lastName = action.payload.lastName
            state.inputs.email = action.payload.email
            state.inputs.bio = action.payload.bio
            state.status = "LOADING"
        },
        loadUpdateUserProfileSuccess: (state) => {
            state.inputs.firstName = ""
            state.inputs.lastName = ""
            state.inputs.email = ""
            state.status = "SUCCESS"
        },
        loadUpdateUserProfileError: (state) => {
            state.inputs.firstName = ""
            state.inputs.lastName = ""
            state.inputs.email = ""
            state.status = "ERROR"
        },
    },
})


const { reducer, actions } = slice
export const { 
    loadUpdateUserProfile, loadUpdateUserProfileSuccess, loadUpdateUserProfileError
} = actions
export default reducer