import React, { useCallback, useEffect, useState } from 'react'
import { connect } from 'react-redux';
import './index.scss'
import Headerfooter from '../../layout/headerfooter'
import profilePicture from '../../../assets/images/festive_hipposhark.png';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileEdit } from '@fortawesome/free-solid-svg-icons';
import Button from '../../components/Button';
import Input from '../../components/Input';
import AccountDetails from './AccountDetails';
import AddAccountModal from '../../modals/AddAccountModal';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { getUserBio, getUserEmail, getUserName, getUserRole, isUserOwner } from '../../../store/reducer/login/user/selector';
import { isUpdateUserProfileError, isUpdateUserProfileSuccess } from '../../../store/reducer/userProfile/generalProfile/updateUserProfile/selector';

import { loadUpdateUserProfile } from '../../../store/reducer/userProfile/generalProfile/updateUserProfile'
import { loadChangePassword } from '../../../store/reducer/userProfile/generalProfile/changePassword'

import { getUserAccounts } from '../../../store/reducer/userProfile/ownerProfile/userAccounts/selector';
import { changeAddAccount, loadAddAccount } from '../../../store/reducer/userProfile/ownerProfile/addUserAccount'
import { loadUserAccounts } from '../../../store/reducer/userProfile/ownerProfile/userAccounts'
import { isAddAccountError, isAddAccountSuccess } from '../../../store/reducer/userProfile/ownerProfile/addUserAccount/selector';
import { isDeleteAccountError, isDeleteAccountSuccess, isUpdateAccountError, isUpdateAccountSuccess } from '../../../store/reducer/userProfile/ownerProfile/currUserAccountSettings/selector';
import { isChangePasswordError, isChangePasswordSuccess } from '../../../store/reducer/userProfile/generalProfile/changePassword/selector';
import ChangePasswordModal from '../../modals/ChangePasswordModal';


const matchStateToProps = (state) => ({
    userName: getUserName(state),
    userRole: getUserRole(state),
    userEmail: getUserEmail(state),
    userBio: getUserBio(state),
    isUserOwner: isUserOwner(state),
    accounts: getUserAccounts(state),
    isUpdateUserProfileSuccess: isUpdateUserProfileSuccess(state),
    isUpdateUserProfileError: isUpdateUserProfileError(state),
    isChangePasswordSuccess: isChangePasswordSuccess(state),
    isChangePasswordError: isChangePasswordError(state),
    isAddAccountSuccess: isAddAccountSuccess(state),
    isAddAccountError: isAddAccountError(state),
    isUpdateAccountSuccess: isUpdateAccountSuccess(state),
    isUpdateAccountError: isUpdateAccountError(state),
    isDeleteAccountSuccess: isDeleteAccountSuccess(state),
    isDeleteAccountError: isDeleteAccountError(state),
})

const matchDispatchToProps = {
    loadUpdateUserProfile,
    loadChangePassword,

    changeAddAccount, loadAddAccount,
    loadUserAccounts,
}

function UserProfile(props) {

    const {
        userName, userRole, userEmail, userBio, isUserOwner,
        changeAddAccount,
        loadUpdateUserProfile,
        loadChangePassword,
        loadAddAccount, isAddAccountSuccess, isAddAccountError,
        loadUserAccounts, accounts,
        isUpdateUserProfileSuccess, isUpdateUserProfileError,
        isChangePasswordSuccess, isChangePasswordError,
        isUpdateAccountSuccess, isUpdateAccountError,
        isDeleteAccountSuccess, isDeleteAccountError,
    } = props

    // owner stuff
    const [currOwnerSettingsMode, setCurrOwnerSettingsMode] = useState("account") // general, account

    const [showAddAccountModal, setShowAddAccountModal] = useState(false) // Add Account Button --> Modal to Add Account

    const [addAccountFirstName, setAddAccountFirstName] = useState("")
    const [addAccountLastName, setAddAccountLastName] = useState("")
    const [addAccountEmail, setAddAccountEmail] = useState("")
    const [addAccountRole, setAddAccountRole] = useState("writer")

    const [addAccountTemporaryPassword, setAddAccountTemporaryPassword] = useState("")

    const handleAddAccount = () => {
        if (addAccountFirstName !== "" && addAccountLastName !== "" && addAccountEmail !== "" && addAccountTemporaryPassword !== "") {
            changeAddAccount({
                firstName: addAccountFirstName,
                lastName: addAccountLastName,
                email: addAccountEmail,
                role: addAccountRole,
                bio: "",
                temporaryPassword: addAccountTemporaryPassword,
                authenticated: false,
                disabled: false,
            })
            loadAddAccount()
            setAddAccountFirstName("")
            setAddAccountLastName("")
            setAddAccountEmail("")
            setAddAccountRole("writer")

            loadUserAccounts()
            setShowAddAccountModal(false);
        } else {
            toast.warning("Please Fill In All of the Inputs")
        }

    }

    const generateTemporaryPassword = useCallback(() => {
        const md5 = require('md5')
        let md5password = md5(`${addAccountFirstName}${addAccountLastName}${addAccountEmail}${addAccountRole}`).slice(0, 10)
        setAddAccountTemporaryPassword(md5password)
    }, [addAccountFirstName, addAccountLastName, addAccountEmail, addAccountRole])

    // Profile stuff
    const firstName = userName.firstName
    const lastName = userName.lastName
    const role = userRole.slice(0, 1).toUpperCase() + userRole.slice(1)
    const email = userEmail
    const bio = userBio

    const [profileFirstName, setProfileFirstName] = useState(firstName)
    const [profileLastName, setProfileLastName] = useState(lastName)
    const [profileEmail] = useState(email)
    const [profileBio, setProfileBio] = useState(bio)

    const [showChangePasswordModal, setShowChangePasswordModal] = useState(false)

    const [changePasswordOld, setChangePasswordOld] = useState("")
    const [changePasswordNew, setChangePasswordNew] = useState("")
    const [changePasswordConfirmNew, setChangePasswordConfirmNew] = useState("")

    const handleUpdateProfile = () => {
        if (firstName !== profileFirstName || lastName !== profileLastName || email !== profileEmail || bio !== profileBio) {
            loadUpdateUserProfile({
                firstName: profileFirstName.toLowerCase(),
                lastName: profileLastName.toLowerCase(),
                email: profileEmail,
                bio: profileBio,
            })
        } else {
            toast.warning("Please Change Something!")
        }
    }

    const handleChangePassword = () => {
        // console.log(`changePasswordOld: ${changePasswordOld}\nchangePasswordOld: ${changePasswordNew}\nchangePasswordOld: ${changePasswordConfirmNew}\n`)
        if (changePasswordOld !== "" && changePasswordNew !== "" && changePasswordConfirmNew !== "") {
            loadChangePassword({
                oldPassword: changePasswordOld,
                newPassword: changePasswordNew,
                confirmNewPassword: changePasswordConfirmNew
            })
        } else {
            toast.warning("Please fill in all of the Inputs!")
        }
        setChangePasswordOld("")
        setChangePasswordNew("")
        setChangePasswordConfirmNew("")
    }

    // Use Effects

    useEffect(() => {
        if (isUserOwner) {
            loadUserAccounts()
        }
    }, [isUserOwner, loadUserAccounts])

    useEffect(() => {
        if (addAccountTemporaryPassword === "") {
            if (addAccountFirstName !== "" && addAccountLastName !== "" && addAccountEmail !== "") {
                generateTemporaryPassword()
            }
        } else {
            if (addAccountFirstName !== "" && addAccountLastName !== "" && addAccountEmail !== "") {
                generateTemporaryPassword()
            } else {
                setAddAccountTemporaryPassword("")
            }
        }

    }, [addAccountFirstName, addAccountLastName, addAccountEmail, addAccountTemporaryPassword,
        generateTemporaryPassword, setAddAccountTemporaryPassword
    ])

    useEffect(() => { // Update YOUR Account Settings
        if (isUpdateUserProfileSuccess) {
            toast.success("User Profile Updated Successfully! ")
        }
        if (isUpdateUserProfileError) {
            toast.error("User Profile Failed to Update! ")
        }
    }, [isUpdateUserProfileSuccess, isUpdateUserProfileError])

    useEffect(() => {
        if (isChangePasswordSuccess) {
            toast.success("Successfully Changed Your Password! ")
            setShowChangePasswordModal(false)
        }
        if (isChangePasswordError) {
            toast.error("Failed to Change Your Password! ")
        }
    }, [isChangePasswordSuccess, setShowChangePasswordModal, isChangePasswordError])

    useEffect(() => { // Update USER Account Settings
        if (isUpdateAccountSuccess) {
            toast.success("Successfully Updated Account! ")
        }
        if (isUpdateAccountError) {
            toast.error("Failed to Update Account! ")
        }
    }, [isUpdateAccountSuccess, isUpdateAccountError])

    useEffect(() => { // Delete USER Account
        if (isDeleteAccountSuccess) {
            toast.success("Successfully Deleted Account! ")
        }
        if (isDeleteAccountError) {
            toast.error("Failed to Delete Account! ")
        }
    }, [isDeleteAccountSuccess, isDeleteAccountError])

    useEffect(() => { // Add Account
        if (isAddAccountSuccess) {
            toast.success("Successfully Added Account! ")
        }
        if (isAddAccountError) {
            toast.error("Failed to Add Account! ")
        }

    }, [isAddAccountSuccess, isAddAccountError])

    return (
        <Headerfooter title="left">
            <div className='user-profile-background'>
                <div className='user-profile-settings'>
                    <div className='user-profile-details'>
                        <div className='user-profile-details title'>
                            Your Public Profile
                        </div>
                        <div className='user-profile-details name'>
                            <div className='user-profile-details name firstName'>
                                First Name: <Input width="medium" defaultValue={profileFirstName.slice(0, 1).toUpperCase() + profileFirstName.slice(1)} onChange={setProfileFirstName}>Your Email Address</Input>
                            </div>
                            <div className='user-profile-details name lastName'>
                                Last Name: <Input width="medium" defaultValue={profileLastName.slice(0, 1).toUpperCase() + profileLastName.slice(1)} onChange={setProfileLastName}>Your Email Address</Input>
                            </div>
                        </div>
                        <div className='user-profile-details email'>
                            {/* Email: <Input width="long" defaultValue={profileEmail} onChange={setProfileEmail}>Your Email Address</Input> */}
                            Email: {profileEmail}
                        </div>
                        <div>
                            Bio: <Input defaultValue={profileBio} onChange={setProfileBio}>Describe Yourself!</Input>
                        </div>
                        <div className='user-profile-details role'>
                            Role: {role}
                        </div>
                        <div className='user-profile-details update-profile'>
                            <Button color="green" onClick={() => { handleUpdateProfile() }}>Update Profile</Button>
                        </div>
                        <div className='user-profile-details change-password'>
                            <Button color="green" onClick={() => { setShowChangePasswordModal(true) }}>Change Password</Button>
                        </div>
                    </div>
                    <div className='user-profile-picture'>
                        <img className='user-profile-picture profile-picture picture' src={profilePicture} alt="profile" />
                        <div className='user-profile-picture profile-picture edit'>
                            Edit &nbsp;&nbsp;<FontAwesomeIcon icon={faFileEdit} />
                        </div>
                    </div>
                </div>

                {isUserOwner && <div className='owner-settings'>
                    <div className='owner-settings-tabs'>
                        <div className={`settings-tab ${currOwnerSettingsMode === "general" ? "open" : "closed"}`} onClick={() => { setCurrOwnerSettingsMode("general") }}>
                            General Settings
                        </div>
                        <div className={`settings-tab ${currOwnerSettingsMode === "account" ? "open" : "closed"}`} onClick={() => { setCurrOwnerSettingsMode("account") }}>
                            Account Settings
                        </div>
                        <div className='settings-tab-empty'>
                        </div>
                    </div>
                    <div className='owner-settings controls'>
                        {
                            currOwnerSettingsMode === "general" ?
                                <div className='general-settings-content'>
                                    General Settings Content
                                </div>
                                :
                                <div className='account-settings-content'>
                                    <div className='account-settings-content add-account-button'>
                                        <Button size="long" onClick={() => { setShowAddAccountModal(true) }}>
                                            Add Account
                                        </Button>
                                    </div>
                                    <div className='account-settings-content accounts'>
                                        <div className='owner-accounts'>
                                            <div className='owner-accounts label'>
                                                Owners
                                            </div>
                                            <div className='owner-accounts accounts'>
                                                {accounts.owners.map((curr, index) =>
                                                    <AccountDetails key={index} account={curr} role="owner" color="yellow" />
                                                )}
                                            </div>
                                        </div>
                                        <div className='editor-accounts'>
                                            <div className='editor-accounts label'>
                                                Editors
                                            </div>
                                            <div className='editor-accounts accounts'>
                                                {accounts.editors.map((curr, index) =>
                                                    <AccountDetails key={index} account={curr} role="editor" color="green" />
                                                )}
                                            </div>
                                        </div>
                                        <div className='writer-accounts'>
                                            <div className='writer-accounts label'>
                                                Writers
                                            </div>
                                            <div className='writer-accounts accounts'>
                                                {accounts.writers.map((curr, index) =>
                                                    <AccountDetails key={index} account={curr} role="writer" color="blue" />
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                        }
                    </div>
                </div>}
            </div>
            <ChangePasswordModal
                showChangePasswordModal={showChangePasswordModal}
                setShowChangePasswordModal={setShowChangePasswordModal}
                changePasswordOld={changePasswordOld}
                setChangePasswordOld={setChangePasswordOld}
                changePasswordNew={changePasswordNew}
                setChangePasswordNew={setChangePasswordNew}
                changePasswordConfirmNew={changePasswordConfirmNew}
                setChangePasswordConfirmNew={setChangePasswordConfirmNew}
                handleChangePassword={handleChangePassword}
            />
            <AddAccountModal
                showAddAccountModal={showAddAccountModal}
                setShowAddAccountModal={setShowAddAccountModal}
                setAddAccountFirstName={setAddAccountFirstName}
                setAddAccountLastName={setAddAccountLastName}
                setAddAccountEmail={setAddAccountEmail}
                addAccountTemporaryPassword={addAccountTemporaryPassword}
                addAccountRole={addAccountRole}
                setAddAccountRole={setAddAccountRole}
                handleAddAccount={handleAddAccount}
            />


        </Headerfooter>
    )
}

