export const getUserUpdateProfile = (state) => {
    return state.userProfile.generalProfile.updateUserProfile.inputs
}

export const isUpdateUserProfileSuccess = (state) => {
    return state.userProfile.generalProfile.updateUserProfile.status === "SUCCESS"
}

export const isUpdateUserProfileError = (state) => {
    return state.userProfile.generalProfile.updateUserProfile.status === "ERROR"
}




