import React from 'react'
import Button from '../../components/Button'
import Input from '../../components/Input'
import Modal from '../../components/Modal'
import "./index.scss"

export default function DeleteAccountModal(props) {
    const {
        showDeleteAccountModal,
        setShowDeleteAccountModal,
        firstName,
        lastName,
        setAccountDeletionConfirmation,
        handleDeleteAccountSettings,
        setShowAccountSettingsModal,
        accountDeletionConfirmation
    } = props
    return (
        <Modal height={500} width={400} show={showDeleteAccountModal} handleShow={() => { setShowDeleteAccountModal(false) }}>
            <div className='account-deletion-modal'>
                <div className='account-deletion-modal title'>
                    <strong>{firstName} {lastName}</strong>
                </div>
                <div className='account-deletion-modal prompt'>
                    Input the Full Name of the User to <strong>Permanently</strong> Delete their Account
                </div>
                <div className='account-deletion-modal name-input'>
                    <Input width="medium" onChange={setAccountDeletionConfirmation} maxLength={700}></Input>
                </div>
                <div className='account-deletion-modal controls'>
                    <Button color="green" onClick={() => {setShowDeleteAccountModal(false);}}>
                        Cancel
                    </Button>
                    <Button color="red" onClick={() => {handleDeleteAccountSettings();setShowDeleteAccountModal(false);setShowAccountSettingsModal(false);}} disabled={accountDeletionConfirmation !== `${firstName} ${lastName}`}>
                        Delete Account
                    </Button>
                </div>
            </div>
        </Modal>
    )
}
