import { combineReducers } from "redux";
import addUserAccountReducer from "./addUserAccount";
import currUserAccountSettingsReducer from "./currUserAccountSettings";
import userAccountsReducer from "./userAccounts";

export default combineReducers({
    addUserAccount: addUserAccountReducer,
    currUserAccountSettings: currUserAccountSettingsReducer,
    userAccounts: userAccountsReducer,
})