import React, { useMemo } from 'react'
import "./index.scss"
import CompositeTemplate from '../../../../components/ArticlesPreview/CompositeTemplate'
import ImageTemplate from '../../../../components/ArticlesPreview/ImageTemplate'
import SimpleTemplate from '../../../../components/ArticlesPreview/SimpleTemplate'

function StandardTemplate(props) {

    const { currentPreviewArticles } = props;

    const thirdRowArticles = useMemo(() => currentPreviewArticles.slice(6), [currentPreviewArticles])

    return (
        <div className="standard-template">
            <div className="standard-template-article-column">
                <div className="standard-template-section-one">
                    <div className="standard-template-section-one-simple-templates-image">
                        {
                            currentPreviewArticles[0] && <ImageTemplate
                                key={0}
                                id={currentPreviewArticles[0].id}
                                image={currentPreviewArticles[0].img.url}
                                category={currentPreviewArticles[0].category}
                                title={currentPreviewArticles[0].title}
                                author={currentPreviewArticles[0].author}
                                date={currentPreviewArticles[0].date}
                            />
                        }
                    </div>


                    <div className="standard-template-section-one-simple-templates">
                        <div className="standard-template-section-one-simple-templates-simple" >
                            {
                                currentPreviewArticles[1] && <SimpleTemplate key={1} id={currentPreviewArticles[1].id} category={currentPreviewArticles[1].category} title={currentPreviewArticles[1].title} author={currentPreviewArticles[1].author} date={currentPreviewArticles[1].date}>
                                    {currentPreviewArticles[1].preview}
                                </SimpleTemplate>
                            }
                        </div>
                        <div style={{}}>
                            {
                                currentPreviewArticles[2] && <SimpleTemplate key={2} id={currentPreviewArticles[2].id} category={currentPreviewArticles[2].category} title={currentPreviewArticles[2].title} author={currentPreviewArticles[2].author} date={currentPreviewArticles[2].date}>
                                    {currentPreviewArticles[2].preview}
                                </SimpleTemplate>
                            }
                        </div>

                    </div>
                </div>

                <div className="standard-template-section-two">
                    <div className="standard-template-section-two-simple-templates">
                        <div className="standard-template-section-two-simple-templates-simple" >
                            {    
                                currentPreviewArticles[3] && <SimpleTemplate key={3} id={currentPreviewArticles[3].id} category={currentPreviewArticles[3].category} title={currentPreviewArticles[3].title} author={currentPreviewArticles[3].author} date={currentPreviewArticles[3].date}>
                                    {currentPreviewArticles[3].preview}
                                </SimpleTemplate>
                            }
                        </div>
                        <div >
                            {    
                                currentPreviewArticles[4] && <SimpleTemplate key={4} id={currentPreviewArticles[4].id} category={currentPreviewArticles[4].category} title={currentPreviewArticles[4].title} author={currentPreviewArticles[4].author} date={currentPreviewArticles[4].date}>
                                    {currentPreviewArticles[4].preview}
                                </SimpleTemplate>
                            }
                        </div>
                    </div>
                    <div className="standard-template-section-two-simple-templates-image" >
                        {
                            currentPreviewArticles[5] && <ImageTemplate
                                key={5}
                                id={currentPreviewArticles[5].id}
                                image={currentPreviewArticles[5].img.url}
                                category={currentPreviewArticles[5].category}
                                title={currentPreviewArticles[5].title}
                                author={currentPreviewArticles[5].author}
                                date={currentPreviewArticles[5].date}
                            />
                        }
                    </div>

                </div>

                <div className="standard-template-section-three">
                    {
                        thirdRowArticles.map((article, index) =>
                            <>
                                {
                                    article && <div key={index} className="standard-template-section-three-composite-template" >
                                        <CompositeTemplate id={currentPreviewArticles[index + 6].id} image={article.img.url} category={article.category} title={article.title} author={article.author} date={article.date}>
                                            {article.preview}
                                        </CompositeTemplate>
                                    </div>
                                }
                            </>
                        )
                    }
                </div>
            </div>
            <div className="standard-template-information-column">
                <div>
                </div>
            </div>
        </div>
    )
}

export default StandardTemplate
