import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    updateStatus : "idle",
    deleteStatus: "idle",
    error : "",
    settings : {
        id : null,
        firstName : "",
        lastName : "",
        email : "",
        role : "",
        disabled : false,
        temporaryPassword : "",
        authenticated : false,
    }
    
}

const slice = createSlice({
    initialState,
    name : "currUserAccountSettings",
    reducers : {
        changeCurrAccountSettings : (state, action) => {
            state.settings.id = action.payload.id
            state.settings.firstName = action.payload.firstName
            state.settings.lastName = action.payload.lastName
            state.settings.email = action.payload.email
            state.settings.role = action.payload.role
            state.settings.disabled = action.payload.disabled
            state.settings.authenticated = action.payload.authenticated
            state.settings.temporaryPassword = action.payload.temporaryPassword
        },

        loadUpdateAccount : (state) => {
            state.updateStatus = "LOADING"
        },
        loadUpdateAccountSuccess : (state) => {
            state.updateStatus = "SUCCESS"
        },
        loadUpdateAccountError : (state) => {
            state.updateStatus = "ERROR"
        },

        loadDeleteAccount : (state) => {
            state.deleteStatus = "LOADING"
        },
        loadDeleteAccountSuccess : (state) => {
            state.deleteStatus = "SUCCESS"
        },
        loadDeleteAccountError: (state) => {
            state.deleteStatus = "ERROR"
        },
    },
})


const {reducer, actions} = slice
export const {
    changeCurrAccountSettings, 
    loadUpdateAccount, loadUpdateAccountSuccess, loadUpdateAccountError,
    loadDeleteAccount, loadDeleteAccountSuccess, loadDeleteAccountError,
} = actions
export default reducer