import axios from "axios";
// import { BASE_URL } from "../../mocks/handler";

console.log(`api jwt: ${localStorage.getItem("token")}`)
// export const BASE_URL = "http://localhost:4444"
export const BASE_URL = process.env.REACT_APP_API_URL

// User APIs

export const loginUser = async (email, password) => {
    const { data } = await axios.post(`${BASE_URL}/user/login`, { email, password })
    return data
}

export const registerUser = async (account) => {
    const { data } = await axios.post(`${BASE_URL}/user/register`, { account })
    return data
}

export const refreshToken = async () => {
    const token = localStorage.getItem("token")
    const { newToken } = await axios.post(`${BASE_URL}/user/refreshToken`, {}, {
        headers: { "Authorization": `Bearer ${token}`, }
    })
    return newToken
}

export const authenticateUser = async (_id, newPassword, confirmNewPassword,) => {
    const token = localStorage.getItem("token")
    console.log("API: ", newPassword, confirmNewPassword)
    const { data } = await axios.post(`${BASE_URL}/user/authenticateUser`, { _id, newPassword, confirmNewPassword, }, {
        headers: { "Authorization": `Bearer ${token}`, }
    })
    return data
}

export const updateUserPassword = async (_id, oldPassword, newPassword, confirmNewPassword) => {
    const token = localStorage.getItem("token")
    const { data } = await axios.post(`${BASE_URL}/user/changePassword`, { _id, oldPassword, newPassword, confirmNewPassword }, {
        headers: { "Authorization": `Bearer ${token}`, }
    })
    return data
}

