import React from 'react'
import "./index.scss"

export default function StyleButton(props) {
  const {
    onClick=()=>{},
    active=false, // Indicates if the button/style is active
    children, // The Label
  } = props

  const onToggle = (e) => {
    e.preventDefault()
    onClick()
  }

  return (
    <div className = {"style-button " + (active ? "active" : "inactive")} onMouseDown = {onToggle}>
      {children}
    </div>
  )
}
