import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../services/api";
import { loadAuthenticationPasswordError, loadAuthenticationPasswordSuccess } from ".";
import { historyObject } from "../../../../historyObject";
import { getAuthentication } from "./selector";
import { changeUser } from "../user";
import { getUserId } from "../user/selector";

const getResponseError = (e) => {
    if (e.response && e.response.data){
        return e.response.data
    }
    return {
        status : 500,
        type : "unkown-error",
    }
}

export function* loginAuthenticationSaga(){
    try {
        const _id = yield select(getUserId)
        const {newPassword, confirmNewPassword} = yield select(getAuthentication)
        const data = yield call([api, api.authenticateUser], _id, newPassword, confirmNewPassword, )
        yield put(loadAuthenticationPasswordSuccess())
        yield put(changeUser(data))
        historyObject.push("/")
    } catch(e){
        yield put(loadAuthenticationPasswordError(getResponseError(e)))
        console.log("Error from Authenticating User Saga: ", e)
    }
}
