import { createSlice } from "@reduxjs/toolkit";
// import { changeUser } from "../user";

//TODO
const initialState = {
    status: "idle",
    error: "",
    inputs : {
        newPassword : "",
        confirmNewPassword : "",
    },
}

const slice = createSlice({
    initialState,
    name: "authentication",
    reducers: {
        loadAuthenticationPassword: (state, action) => {
            state.inputs.newPassword = action.payload.newPassword
            state.inputs.confirmNewPassword = action.payload.confirmNewPassword
            state.status = "LOADING"
        },
        loadAuthenticationPasswordSuccess: (state) => {
            state.inputs.newPassword = ""
            state.inputs.confirmNewPassword = ""
            state.status = "SUCCESS"
        },
        loadAuthenticationPasswordError: (state) => {
            state.inputs.newPassword = ""
            state.inputs.confirmNewPassword = ""
            state.status = "ERROR"
        }
    },
})


const { reducer, actions } = slice
export const { 
    loadAuthenticationPassword, loadAuthenticationPasswordSuccess, loadAuthenticationPasswordError
} = actions
export default reducer