import React, { useEffect, useState } from 'react'
import "./index.scss";
import Button from '../../components/Button'
import Input from '../../components/Input'
import { loadUser, changeUser } from "../../../store/reducer/login/user";
import { connect } from 'react-redux';
import { getUser, isAuthenticated, isDisabled, isLoggedIn, isLoginError, isLoginSuccess } from '../../../store/reducer/login/user/selector';
import { historyObject } from '../../../historyObject';
import HeaderFooter from '../../layout/headerfooter';
import { toast } from 'react-toastify';

const mapStateToProps = (state) => ({
    user: getUser(state),
    loggedIn: isLoggedIn(state),
    isAuthenticated: isAuthenticated(state),
    isDisabled: isDisabled(state),
    isLoginSuccess: isLoginSuccess(state),
    isLoginError: isLoginError(state),
})

const mapDispatchToProps = { loadUser, changeUser }

function Login(props) {
    const { user, loggedIn, isAuthenticated, isDisabled, isLoginSuccess, isLoginError, loadUser, changeUser } = props

    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")

    const handleLogin = () => {
        loadUser({
            email: email,
            password: password,
        })
    }

    useEffect(() => {
        if (isLoginSuccess) {
            if (isDisabled) {
                changeUser(null) // For now logs user out of account 
                toast.error("Your account has been disabled! Please contact the Owners to enable it.")
            } else {
                toast.success("Logged in Successfully!")
            }
        }

        if (isLoginError) {
            toast.error("Invalid Credentials!")
        }

    }, [isLoginSuccess, isLoginError, isDisabled, changeUser])

    useEffect(() => {
        if (loggedIn) {
            if (isAuthenticated) {
                historyObject.push("/")
            } else {
                historyObject.push(`/authentication/${user.id}`)
            }
        }
    }, [user, loggedIn, isAuthenticated])

    return (
        <HeaderFooter title={"middle"}>
            <div className="login-background">
                <div className="login-title">
                    Login
                </div>
                <div className="login-information">
                    <h3>Email Address</h3>
                    <Input onChange={setEmail} width={"long"}>
                        Email Address
                    </Input>
                    <h3>Password</h3>
                    <Input type={"password"} onChange={setPassword} width={"long"} background={"white"} rounded={true}>
                        Password
                    </Input>
                    <Button onClick={handleLogin} type={"login"} size={"long"} background={"white"} rounded={true}>
                        Login
                    </Button>
                </div>
            </div>
        </HeaderFooter>

    )
}


export default connect(mapStateToProps, mapDispatchToProps)(Login)