import React, { useEffect, useState } from 'react'
import "./index.scss"

export default function Input(props) {
    const { disabled=false, defaultValue="", onChange = () => { }, children, type, rounded = true, maxLength, width = "default", fontSize = "small", background = "white" } = props

    const [value, setValue] = useState(defaultValue)

    // console.log(value)

    const classes = [
        "input",
        "width-" + width,
        "font-" + fontSize,
        "background-" + background,
        rounded ? "rounded" : "squared",
    ].join(" ")

    const handleChange = (e) => {
        onChange(e.target.value)
        setValue(e.target.value)
    }
    // width is 100% of parent container, alter that to change width of input

    useEffect(() => {
        setValue(defaultValue)
    }, [setValue, defaultValue])

    return (
        <input className={classes} placeholder={children} value={value} onChange={handleChange} type={type} maxLength={maxLength} disabled={disabled}></input>

    )
}