import React from 'react'
import { historyObject } from '../../../../historyObject';
import './index.scss'

export default function PendingArticle(props) {
    const {id, img, title, authorId, authorName, date, category, wordcount} = props
    console.log(authorId)
    return (
        <div className = "pending-article-box" onClick={() => {historyObject.push(`/articleeditingplatform?articleId=${id}&mode=editing`)}}>
            <div className="pending-article-top">
                <img className ="pending-article-img" src = {img ? img.url : img} alt = "article-preview"/>
            </div>
            <div className="pending-article-bottom">
                <div className="pending-article-title">
                    {title}
                </div>
                <div className="pending-article-details">
                    By {authorName} &nbsp;&nbsp;|&nbsp;&nbsp; {date} &nbsp;&nbsp;|&nbsp;&nbsp; {category.toUpperCase()} &nbsp;&nbsp;|&nbsp;&nbsp; {wordcount} words
                    
                </div>

            </div>
            
        </div>
    )
}
