import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../services/api";
import { loadImagesEditorError, loadImagesEditorSuccess } from ".";
import { getArticleImages } from "./selector";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* imagesEditorSaga() {
    try {
        const imgs = select(getArticleImages)
        const formData = new FormData()
        formData.append("file", imgs)
        formData.append("upload_preset", "ykr0zcfj")
        const imgLinks = yield call([api, api.uploadImages], formData)
        yield put(loadImagesEditorSuccess(imgLinks))
    } catch (e) {
        yield put(loadImagesEditorError(getResponseError(e)))
        console.log("Error from Images Editor in Saga: ", e)
    }
}