export const getCurrAccountSettings = (state) => {
    return state.userProfile.ownerProfile.currUserAccountSettings.settings
}

export const isUpdateAccountSuccess = (state) => {
    return state.userProfile.ownerProfile.currUserAccountSettings.updateStatus === "SUCCESS"
}

export const isUpdateAccountError = (state) => {
    return state.userProfile.ownerProfile.currUserAccountSettings.updateStatus === "ERROR"
}

export const isDeleteAccountSuccess = (state) => {
    return state.userProfile.ownerProfile.currUserAccountSettings.deleteStatus === "SUCCESS"
}

export const isDeleteAccountError = (state) => {
    return state.userProfile.ownerProfile.currUserAccountSettings.deleteStatus === "ERROR"
}

