import { call, put, select } from "redux-saga/effects";
import * as api from "../../../../../services/api";
import { loadUpdateAccountSuccess, loadUpdateAccountError, loadDeleteAccountSuccess, loadDeleteAccountError } from ".";
import { getCurrAccountSettings } from "./selector";
import { getUserId } from "../../../login/user/selector";

const getResponseError = (e) => {
    if (e.response && e.response.data) {
        return e.response.data
    }
    return {
        status: 500,
        type: "unkown-error",
    }

}

export function* ownerProfileAccountSettingsSaga() {
    try {
        const account = yield select(getCurrAccountSettings)
        const { id: _id, ...changes } = account
        yield call([api, api.updateAccountList], _id, changes)
        yield put(loadUpdateAccountSuccess())
    } catch (e) {
        yield put(loadUpdateAccountError(getResponseError(e)))
        console.log("Error from Owner Profile Saga (Update Account): ", e)
    }
}

export function* ownerProfileDeleteAccountSaga() {
    try {
        const _id = yield select(getUserId)
        const account = yield select(getCurrAccountSettings)
        if (_id !== account.id) {
            yield call([api, api.deleteAccountList], account.id)
            yield put(loadDeleteAccountSuccess())
        }
    } catch (e) {
        yield put(loadDeleteAccountError(getResponseError(e)))
        console.log("Error from Owner Profile Saga (Delete Account): ", e)
    }
}

