import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    loadingStatus: "idle",
    updatingStatus: "idle",
    deletingStatus: "idle",
    publishEditingStatus: "idle",
    publishPendingStatus: "idle",
    rejectPendingStatus: "idle",
    loadingError: "",
    updatingError: "",
    deletingError: "",
    publishEditingError: "",
    publishPendingError: "",
    rejectPendingError: "",
    status: "", // Article Status
    isAuthor: null,
    currArticleId: null,
    serverArticle: {
        title: "",
        date: "",
        template: "",
        content: "",
        category: "",
        wordcount: "",
        imgs: [],
    },
    currArticle: {
        title: "",
        date: "",
        template: "",
        content: "",
        category: "",
        wordcount: "",
        imgs: [],
    }

}

const slice = createSlice({
    initialState,
    name: "articleEditor",
    reducers: {
        changeStatus: (state, action) => {
            state.status = action.payload
        },
        changeIsAuthor: (state, action) => {
            state.isAuthor = action.payload
        },
        changeCurrEditingArticleId: (state, action) => {
            state.currArticleId = action.payload
        },
        changeCurrEditingArticle: (state, action) => {
            state.currArticle.title = action.payload.title
            state.currArticle.template = action.payload.template
            state.currArticle.date = action.payload.date
            state.currArticle.content = action.payload.content
            state.currArticle.category = action.payload.category
            state.currArticle.wordcount = action.payload.wordcount
            state.currArticle.imgs = action.payload.imgs
        },
        changeCurrServerArticle: (state, action) => {
            state.serverArticle.title = action.payload.title
            state.serverArticle.template = action.payload.template
            state.serverArticle.date = action.payload.date
            state.serverArticle.content = action.payload.content
            state.serverArticle.category = action.payload.category
            state.serverArticle.wordcount = action.payload.wordcount
            state.serverArticle.imgs = action.payload.imgs
        },
        loadUpdateEditingArticle: (state) => {
            state.updatingStatus = "LOADING"
        },
        loadDeleteEditingArticle: (state) => {
            state.deletingStatus = "LOADING"
        },
        loadEditingArticle: (state, action) => {
            state.currArticleId = action.payload
            state.loadingStatus = "LOADING"
        },
        loadPublishEditingArticle: (state) => {
            state.publishEditingStatus = "LOADING"
        },
        loadPublishPendingArticle: (state) => {
            state.publishPendingStatus = "LOADING"
        },
        loadRejectPendingArticle: (state) => {
            state.rejectPendingStatus = "LOADING"
        },
        loadUpdateEditingArticleSuccess: (state, action) => {
            state.serverArticle.title = action.payload.title
            state.serverArticle.template = action.payload.template
            state.serverArticle.date = action.payload.date
            state.serverArticle.content = action.payload.content
            state.serverArticle.category = action.payload.category
            state.serverArticle.wordcount = action.payload.wordcount
            state.serverArticle.imgs = action.payload.imgs
            state.updatingStatus = "SUCCESS"
        },
        loadDeleteEditingArticleSuccess: (state) => {
            state.serverArticle.title = ""
            state.serverArticle.template = ""
            state.serverArticle.date = ""
            state.serverArticle.content = ""
            state.serverArticle.category = ""
            state.serverArticle.wordcount = ""
            state.serverArticle.imgs = []
            state.currArticleId = null
            state.deletingStatus = "SUCCESS"
        },
        loadEditingArticleSuccess: (state, action) => {
            state.serverArticle.title = action.payload.title
            state.serverArticle.template = action.payload.template
            state.serverArticle.date = action.payload.dateSaved
            state.serverArticle.content = action.payload.content
            state.serverArticle.category = action.payload.category
            state.serverArticle.wordcount = action.payload.wordcount
            state.serverArticle.imgs = action.payload.imgs
            state.status = action.payload.status
            state.loadingStatus = "SUCCESS"
        },

        loadPublishEditingArticleSuccess: (state) => {
            state.publishEditingStatus = "SUCCESS"
        },
        loadPublishPendingArticleSuccess: (state) => {
            state.publishPendingStatus = "SUCCESS"
        },
        loadRejectPendingArticleSuccess: (state) => {
            state.rejectPendingStatus = "SUCCESS"
        },

        loadUpdateEditingArticleError: (state, action) => {
            state.updatingStatus = "ERROR"
            state.updatingError = action.payload
        },
        loadDeleteEditingArticleError: (state, action) => {
            state.deletingStatus = "ERROR"
            state.deletingError = action.payload
        },
        loadEditingArticleError: (state, action) => {
            state.loadingStatus = "ERROR"
            state.loadingError = action.payload
        },
        loadPublishEditingArticleError: (state, action) => {
            state.publishEditingStatus = "ERROR"
            state.publishEditingError = action.payload
        },
        loadPublishPendingArticleError: (state, action) => {
            state.publishPendingStatus = "ERROR"
            state.publishPendingError = action.payload
        },
        loadRejectPendingArticleError: (state, action) => {
            state.rejectPendingStatus = "ERROR"
            state.rejectPendingError = action.payload
        },

        loadUpdateEditingArticleIdle: (state) => {
            state.updatingStatus = "idle"
        },
        loadDeleteEditingArticleIdle: (state) => {
            state.deletingStatus = "idle"
        },
        loadEditingArticleIdle: (state) => {
            state.loadingStatus = "idle"
        },
    },
})


const { reducer, actions } = slice
export const {
    changeStatus, changeIsAuthor, changeCurrEditingArticleId, changeCurrEditingArticle, changeCurrServerArticle,
    loadUpdateEditingArticle, loadEditingArticleSuccess, loadEditingArticleError, loadUpdateEditingArticleIdle,
    loadEditingArticle, loadUpdateEditingArticleSuccess, loadUpdateEditingArticleError, loadEditingArticleIdle,
    loadDeleteEditingArticle, loadDeleteEditingArticleSuccess, loadDeleteEditingArticleError, loadDeleteEditingArticleIdle,

    loadPublishEditingArticle, loadPublishEditingArticleSuccess, loadPublishEditingArticleError,
    loadPublishPendingArticle, loadPublishPendingArticleSuccess, loadPublishPendingArticleError,
    loadRejectPendingArticle, loadRejectPendingArticleSuccess, loadRejectPendingArticleError,
} = actions
export default reducer