import { EditorState, RichUtils } from 'draft-js'
import React from 'react'
import clearFormatting from 'draft-js-clear-formatting'
import StyleButton from './StyleButton'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBold, faItalic, faUnderline, faTextSlash, faUndo, faRedo, faLink } from '@fortawesome/free-solid-svg-icons'
import "./index.scss"
import ColorPicker from './ColorPicker'


export default function EditingToolbar(props) {
  const { editorState, setEditorState } = props

  const currentStyle = editorState.getCurrentInlineStyle(); // Checks current style at cursor location

  const INLINE_STYLES = [
    {
      label: <FontAwesomeIcon icon={faBold} />, style: "BOLD",
      onClick: () => {setEditorState(RichUtils.toggleInlineStyle(editorState, "BOLD"))}
    },
    {
      label: <FontAwesomeIcon icon={faItalic} />, style: "ITALIC",
      onClick: () => {setEditorState(RichUtils.toggleInlineStyle(editorState, "ITALIC"))}
    },
    {
      label: <FontAwesomeIcon icon={faUnderline} />, style: "UNDERLINE",
      onClick: () => {setEditorState(RichUtils.toggleInlineStyle(editorState, "UNDERLINE"))}
    },
    {
      label: <FontAwesomeIcon icon={faTextSlash} />, style: "CLEAR",
      onClick: () => {
        const options = {
          inline: true,
          entities: true,
          lists: true,
          style: true,
        }
        setEditorState(clearFormatting(editorState, options))
      }
    },
  ]

  const onAddLink = () => {
    const selection = editorState.getSelection()
    const link = window.prompt('Paste the link -')
    if (!link) {
      setEditorState(RichUtils.toggleLink(editorState, selection, null))
      return 'handled'
    }
    const content = editorState.getCurrentContent()
    const contentWithEntity = content.createEntity('LINK', 'MUTABLE', { url: link })
    const newEditorState = EditorState.push(editorState, contentWithEntity, 'create-entity')
    const entityKey = contentWithEntity.getLastCreatedEntityKey()
    setEditorState(RichUtils.toggleLink(newEditorState, selection, entityKey))
  }

  return (
    <div className="editing-toolbar">
      <StyleButton onClick={() => setEditorState(EditorState.undo(editorState))}>
        <FontAwesomeIcon icon={faUndo} />
      </StyleButton>
      <StyleButton onClick={() => setEditorState(EditorState.redo(editorState))}>
        <FontAwesomeIcon icon={faRedo} />
      </StyleButton>
      <div className="inline-styles-buttons">
        {
          INLINE_STYLES.map((type) =>
            <StyleButton
              key={type.style}
              onClick={type.onClick}
              active={currentStyle.has(type.style)}
              editorState={editorState}
              setEditorState={setEditorState}
            >{type.label}</StyleButton>
          )
        }
        <ColorPicker
          editorState={editorState}
          setEditorState={setEditorState}
        />
      </div>
      <StyleButton onClick={() => onAddLink()}>
        <FontAwesomeIcon icon={faLink} />
      </StyleButton>
    </div>
  )
}
