import { combineReducers } from "redux";
import createNewArticleReducer from "./createNewArticle";
import generalArticlesReducer from "./generalArticles";
import pendingArticlesReducer from "./pendingArticles";

export default combineReducers({
    createNewArticle: createNewArticleReducer,
    pendingArticles: pendingArticlesReducer,
    generalArticles: generalArticlesReducer,
})