import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    status: "idle",
    error: "",
    accounts: {
        owners: [],
        editors: [],
        writers: [],
    },
}

const slice = createSlice({
    initialState,
    name: "userAccounts",
    reducers: {
        loadUserAccounts: (state) => {
            state.status = "LOADING"
        },
        loadUserAccountsSuccess: (state, action) => {
            state.accounts.owners = action.payload.owners
            state.accounts.editors = action.payload.editors
            state.accounts.writers = action.payload.writers
            state.status = "SUCCESS"
        },
        loadUserAccountError: (state, action) => {
            state.status = "ERROR"
            state.error = action.payload
        }
    },
})


const { reducer, actions } = slice
export const {
    loadUserAccounts, loadUserAccountsSuccess, loadUserAccountError
} = actions
export default reducer