export const getPendingArticlesPerPage = (state) => {
    return state.userOffice.pendingArticles.pendingArticlesPerPage
}

export const getCurrentPendingArticlesPage = (state) => {
    return state.userOffice.pendingArticles.currentPendingArticlePage
}

export const getTotalPendingArticles = (state) => {
    return state.userOffice.pendingArticles.totalPendingArticles
}
export const getPendingArticles = (state) => {
    return state.userOffice.pendingArticles.pendingArticles
}