import React from 'react'
import './index.scss'
import {
    RichUtils,
    KeyBindingUtil,
    EditorState,
} from "draft-js";

export const linkStrategy = (contentBlock, callback, contentState) => {
    contentBlock.findEntityRanges((character) => {
        const entityKey = character.getEntity()
        return (
            entityKey !== null &&
            contentState.getEntity(entityKey).getType() === 'LINK'
        )
    }, callback)
}


export function EditorLink(props) {
    const { contentState, entityKey, children } = props

    const { url } = contentState.getEntity(entityKey).getData()

    return (
        <a className="editor-link" href={url} rel="noopener noreferrer" target="_blank" aria-label={url}>
            {children}
        </a>
    )
}

const addLinkPlugin = {
    keyBindingFn(e, { getEditorState }) {
        const editorState = getEditorState()
        const selection = editorState.getSelection()
        if (selection.isCollapsed()) {
            return
        }
        if (KeyBindingUtil.hasCommandModifier(e) && e.keyCode === 75) {
            return 'add-link'
        }
    },

    handleKeyCommand(command, editorState, { setEditorState }) {
        if (command !== 'add-link') {
            return 'not-handled'
        }
        let link = window.prompt('Paste the link -')
        const selection = editorState.getSelection()
        if (!link) {
            RichUtils.toggleLink(editorState, selection, null)
            return 'handled'
        }
        const content = editorState.getCurrentContent()
        const contentWithEntity = content.createEntity('LINK', 'MUTABLE', { url: link })
        const newEditorState = EditorState.push(editorState, contentWithEntity, 'create-entity')
        const entityKey = contentWithEntity.getLastCreatedEntityKey()
        RichUtils.toggleLink(newEditorState, selection, entityKey)
        return 'handled'
    },

    decorators: [{
        strategy: linkStrategy,
        component: EditorLink,
    }],
}

export default addLinkPlugin
