import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    status : "idle",
    error : "",
    currArticle : {
        id: null, 
        title: "",
        author: "",
        date: "",
        content: "", // content will be edited based on template
        category: "",
        wordcount: 0,
        status: "",
    },

}

const slice = createSlice({ // Whole thing is called a reducer
    initialState,
    name : "viewArticle",
    reducers : { // Not actually called reducers, these are actually called actions
        changeCurrViewingArticle : (state, action) => { // action 1
            state.currArticle.id = action.payload.id
            state.currArticle.title = action.payload.title
            state.currArticle.author = action.payload.author
            state.currArticle.date = action.payload.date
            state.currArticle.content = action.payload.content
            state.currArticle.category = action.payload.category
            state.currArticle.wordcount = action.payload.wordcount
            state.currArticle.status = action.payload.status
        },
        loadCurrViewingArticle : (state, action) => {
            state.currArticle.id = action.payload
            state.status = "LOADING"
        },

        loadSuccess : (state) => {
            state.status = "SUCCESS"
        },

        loadError : (state) => {
            state.status = "ERROR"
        }


    },
})


const {reducer, actions} = slice
export const {changeCurrViewingArticle, loadCurrViewingArticle, loadSuccess, loadError} = actions
export default reducer