import React from 'react'
import "./index.scss"

export default function RadioButtons(props) {
    const {values=[], name='banana', defaultValue, currValue, setValue, disabled=false} = props

    return (
        <div className = "radio-buttons">
            {values.map((value) => {
                return (
                    <span key={value} className="radio-button">
                        <input type="radio" checked={currValue === value} onChange={() => {setValue(value)}} name={name} disabled={disabled } defaultValue={defaultValue}/>
                        {value.slice(0,1).toUpperCase() + value.slice(1)}
                    </span>
                )
            })}
        </div>
    )
}
