import React from 'react'
import "./index.scss"
import { historyObject } from '../../../../../historyObject';
import CompositeTemplate from '../../../../components/ArticlesPreview/CompositeTemplate';

export default function CategorizedTemplate(props) {
    const { currentPreviewArticles, filter} = props;
    console.log(currentPreviewArticles)
    return (
        <div className='categorized-template'>
            <div className='categorized-template-background'>
                <div className='categorized-template-title'>
                    {filter.toUpperCase()}
                </div>
                {
                    currentPreviewArticles.map((article, index) => 
                        <div key={index} className="categorized-template-article-preview" onClick = {() => {historyObject.push(`/article/${currentPreviewArticles[index].id}`)}}>
                            <CompositeTemplate image={article.img.url} category={article.category} title={article.title} author={article.author} date={article.date}>
                                {article.preview}
                            </CompositeTemplate>
                        </div>

                    )
                }
            </div>
        </div>
    )
}
